import ru from './ru';
import en from './en';
import parseObjectByMask from '../utils/objects-utils/parse-object-by-mask.js'

const dictionary = (locale) => {
    let currentLocale = null;
    switch (locale) {
        case 'ru': {
            currentLocale = ru;
            break;
        }
        case 'en': {
            currentLocale = en;
            break;
        }
        default: {
            currentLocale = ru;
            break;
        }
    }

    return parseObjectByMask(currentLocale, en);
}

export default dictionary;
