import React from 'react';

import './confirm-form.css';

import { withLocalization } from '../../../hoc';

const ConfirmForm = ({ confirmText, onConfirm, onCancel, localization }) => {
    const { confirm } = localization;
    return (
        <div className = "confirm-form container-fluid">
            <div className="confirm-form-icon">
                <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
            </div>
            <div className="confirm-form-text">{confirmText}</div>
            <div className = "btn-group">
                <button className="btn btn-default" onClick = {onConfirm}>{confirm.confirm}</button>
                <button className="btn btn-danger" onClick = {onCancel}>{confirm.cancel}</button>
            </div>
        </div>
    )
}

export default withLocalization(ConfirmForm);
