import React from 'react';
import CheckBox from "react-animated-checkbox";

import RadioSwitch from '../../../common/radio-switch';

import './channel-settings.css'

const ChannelSettings = ({
	title="title",
	dataDescription = {},

	channelName="fieldName",
	listRadio,

	channelValue="fieldValue",
	isActiveChannel=false,
	selectedRadio=0,

	currentFieldValue="currentFieldValue",

	isValid=true,
	notValidDescription,

	onChange=()=>()=>{},

	active=true
}) => {
	const { description='description', requires='requires' } = dataDescription;
	const radioColor = selectedRadio === 1 ? {backgroundColor: '#d4eff4'} : {backgroundColor: '#dcf3d7'};

	const isCheckBoxActive = active ? null : {cursor:'not-allowed'};
	const checkBoxCheckedColor = active ? "#647dfd" : "#aaa";
	const checkBoxUnCheckedColor = active ? "#7b8ff8" : "#aaa";
	const isLabelActive = active && isActiveChannel ? null : {disabled: 'disabled', cursor:'not-allowed'};
	const isItemActive = active && isActiveChannel ? radioColor : {backgroundColor: '#eee', cursor:'not-allowed', color: '#666'};
	const isInputActive = active && isActiveChannel ? null : {backgroundColor: '#eee', cursor:'not-allowed'};
	const isRadioActive = active && isActiveChannel;

	const onChangeLabel = active ? onChange(channelName, 'if') : () => {};
	const onChangeEnable = active ? onChange(channelName, 'enable') : () => {};
	const onChangeRadio = active ? onChange(channelName, 'radio') : () => {};

	const channelSettingsCaution = notValidDescription !== null ? <WrongValidInfo notValidDescription={notValidDescription}/> : null;
	const inputValidStyle = notValidDescription !== null ? {boxShadow:'0 0 2px 1px #f55', borderColor: '#f00'} : null;

	return (
		<div className="col-sm-4 centered">
			<div className="channel-settings" style={isItemActive}>
				<span className="dropdown info-dropdown">
					<div className="dropdown-menu">
						<div className="description">
							{description}
						</div>
						<div className="requires">
							{requires}
						</div>
					</div>


					<button className="dropdown-toggle info-circle" data-toggle="dropdown">
						<i className="fa fa-info-circle" aria-hidden="true"></i>
					</button>
				</span>

				<span className="channel-settings-check-box">
					<CheckBox
						checked={isActiveChannel}
						checkBoxStyle={{
							checkedColor: checkBoxCheckedColor,
							size: 25,
							unCheckedColor: checkBoxUnCheckedColor
						}}
						style={isCheckBoxActive}
						duration={40}
						onClick={() => onChangeEnable(!isActiveChannel)}
					/>
				</span>

				<div className="title">
					{title}:
				</div>

				<div className="input-div container-fluid">
					{channelSettingsCaution}

					<input
						{...isLabelActive}
						style={{...isInputActive, ...inputValidStyle}}
						type="text"
						step="100000"
						value={channelValue}
						onChange={(item) => onChangeLabel(item.target.value)}
					/>

					<RadioSwitch
						onSelect={(item) => onChangeRadio(item)}
						selected={selectedRadio}
						items={listRadio}
						styles={{
							radio_0:{float: 'left'},
							radio_1:{float: 'right'}
						}}
						isActive={isRadioActive}
					/>

				</div>
			</div>
		</div>
	)
};

const WrongValidInfo = ({notValidDescription}) => {
	return (
		<span className="dropdown info-error-dropdown">
			<div className="dropdown-menu">
				<div className="text-label-description">
					{notValidDescription}
				</div>
			</div>

			<button className="dropdown-toggle info-error" data-toggle="dropdown">
				<i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
			</button>
		</span>
	)
}

export default ChannelSettings;
