import React from 'react';

import ErrorIndicator from '../../../common/error-indicator';

import './layout.css';

const AppDashboardSettingsLayout = ({ form, buttons, error=null }) => {
    const err = (error !== null) ? <ErrorIndicator error={error} /> : null;
    return (
        <div className="layout app-dashboard-settings-layout">
            {err}
            {form}
            {buttons}
        </div>
    )
}

export default AppDashboardSettingsLayout;
