import parseObjectByMask from '../utils/objects-utils/parse-object-by-mask.js';

const resetErrors = (store) => {
    const newData = {
        global_conf: {
            error: null,
            dataState: store.global_conf.dataState === 'ERROR' ? 'EMPTY' : undefined
        },
        network_conf: {
            error: null,
            dataState: store.network_conf.dataState === 'ERROR' ? 'EMPTY' : undefined
        },
        conf_3G: {
            error: null,
            dataState: store.conf_3G.dataState === 'ERROR' ? 'EMPTY' : undefined
        },
        frequency_conf: {
            error: null,
            dataState: store.frequency_conf.dataState === 'ERROR' ? 'EMPTY' : undefined
        },
        frequency_presets: {
            presetsDataState: {},
            presetsDataErrors: {},
            error: null,
            listState: 'EMPTY'
        },
        appSettings: {
            dashboardSettings: {
                error: null,
                dataState: store.appSettings.dashboardSettings.dataState === 'ERROR' ? 'EMPTY' : undefined
            },
            managerSettings: {
                error: null,
                dataState: store.appSettings.managerSettings.dataState === 'ERROR' ? 'EMPTY' : undefined
            },
            passwordSettings: {
                error: null
            }
        },
        deviceActionsError: null,
        aboutState: 'EMPTY',
        aboutError: null,
    }

    //obj, mask, isFillDefault, isReturnEmptyObjects
    return parseObjectByMask(newData, store, true, true);
}

const authorizationReducer = (store, action) => {
    switch (action.type) {
        case 'LOGIN': {
            const { payload } = action;
            return {
                ...resetErrors(store),
                isLogin: true,
                loginToken: payload.token,
                isSupport3G: payload.isSupport3G
            }
        }
		case 'LOGOUT': {
            return {
                ...resetErrors(store),
                isLogin: false,
                loginToken: null
            }
        }
        default: return {};
    }
}

export default authorizationReducer;

