import React from 'react';
import CheckBox from "react-animated-checkbox";

import './check-box.css';

const CheckBoxItem = ({
    title,
    dataDescription = {},
    fieldName,
    fieldValue,
    currentFieldValue,
    isValid,
    notValidDescription,
    onToggleCheckbox,
    active = true
}) => {
    const { description='', requires='' } = dataDescription;

    const checkedColor = active ? "#4b68f9" : "#aaa";
    const unCheckedColor = active ? "#7b8ff8" : "#aaa";
    const divStyle = active ? null : {backgroundColor: '#eee', cursor:'not-allowed', color: '#666'};
    const inputStyle = active ? null : {cursor:'not-allowed'};
    const action = active ? () => onToggleCheckbox(fieldName) : () => {};

    return (
        <div className="col-sm-12">
			<div className="check-box" style={divStyle}>
				<div className="input-div">
					<span className="dropdown">
						<div className="dropdown-menu">
                            <div className="description">
                                {description}
                            </div>
                            <div className="requires">
                                {requires}
                            </div>
						</div>
                        {title}
                        <span className="input-check-box">
                            <CheckBox
                                checked={fieldValue}
                                checkBoxStyle={{
                                    checkedColor: checkedColor,
                                    size: 25,
                                    unCheckedColor: unCheckedColor
                                }}
                                style={inputStyle}
                                duration={40}
                                onClick={action}
                            />
                        </span>


						<button className="dropdown-toggle" data-toggle="dropdown">
							<i className="fa fa-info-circle" aria-hidden="true"></i>
						</button>
					</span>

				</div>
            </div>
		</div>
    )
}

export default CheckBoxItem;
