import { splitFrequencyData, prepareFrequencyData } from '../utils/split-frequency-data';
import { frequencyValidator } from '../utils/page-data-validators';

const frequencyPresetsFileLoaded = (payload) => {
    return {
        type: 'FREQUENCY_PRESETS_FILE_LOADED',
        payload
    }
}

const onSavePresetFile = (configs) => {
    const configsJson = JSON.stringify(configs, null, '\t');
    download(configsJson, "vega-base-station-frequency-settings.json", "text/plain");
}

const onOpenPresetFile = (dispatch, localization) => {
    const input = document.createElement('input');
    input.type = 'file';

    input.onchange = (e) => {
        // getting a hold of the file reference
        const file = e.target.files[0];

        // setting up the reader
        const reader = new FileReader();
        reader.readAsText(file, 'UTF-8');

        // here we tell the reader what to do when it done reading...
        reader.onload = (readerEvent) => {
            const content = readerEvent.target.result; // this is the content!

            if (IsJsonString(content)) {
                const loadedObject = JSON.parse(content);

                const splitedData = prepareFrequencyData(splitFrequencyData(loadedObject));

                const {
                    descriptionOfNotValid,
                    isDataValid
                } = frequencyValidator(splitedData.data, splitedData.radios);

                const result = {
                    ...splitedData,
                    isDataValid,
                    descriptionOfNotValid,
                }

                console.log(file.name, '- is opened.');
                dispatch(frequencyPresetsFileLoaded(result));
            } else {
                alert(localization.warnings.File_exists_not_correct_JSON);
            }
        }
    }
    input.click();
}

export {
    onSavePresetFile,
    onOpenPresetFile
}

const IsJsonString = (str) => {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

// Function to download data to a file
function download(data, filename, type) {
    const file = new Blob([data], {type: type});
    if (window.navigator.msSaveOrOpenBlob) // IE10+
        window.navigator.msSaveOrOpenBlob(file, filename);
    else { // Others
        var a = document.createElement("a"),
                url = URL.createObjectURL(file);
        a.href = url;
        a.download = filename;
        document.body.appendChild(a);
        a.click();
        setTimeout(function() {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);
        }, 0);
    }
}
