import React from 'react';
import { NavLink } from 'react-router-dom';
import ExpertModeToggle from '../expert-mode-toggle';

import './main-menu.css';

const MainMenu = ({
    isSupport3G,
    globalConfIsChanged,
    networkConfIsChanged,
    apn3GConfIsChanged,
    frequencyConfIsChanged,
    isAvailableUpdate,

    localization
}) => {

    const ico =  <i className="fa fa-pencil-square state" aria-hidden="true"></i>;
    const globalConfChangedIco = globalConfIsChanged ? ico : null;
    const networkConfChangedIco = networkConfIsChanged ? ico : null;
    const apn3GConfChangedIco = apn3GConfIsChanged ? ico : null;
    const frequencyConfChangedIco = frequencyConfIsChanged ? ico : null;

    const availableUpdateIco = isAvailableUpdate ? <i className="fa fa-exclamation-circle state" aria-hidden="true"></i> : null;

    const conf3G = isSupport3G ? <li><NavLink to="/3g" activeClassName="selected">
        {apn3GConfChangedIco}
        <div className="menu-item">
            {localization.$3G.title}
        </div>
    </NavLink></li> : null;

    return (
        <div className="main-menu container-fluid">
            <ul className="nav">

                {/* LoRa gateway configs */}
                <li><NavLink exact to="/" activeClassName="selected">
                    {globalConfChangedIco}
                    <div className="menu-item">
                        {localization.loraGlobalGateway.title}
                    </div>
                </NavLink></li>

                {/* Device network configs */}
                <li><NavLink to="/network" activeClassName="selected">
                    {networkConfChangedIco}
                    <div className="menu-item">
                        {localization.deviceNetwork.title}
                    </div>
                </NavLink></li>

                {/* LoRa frequency configs */}
                <li><NavLink to="/frequency" activeClassName="selected">
                    {frequencyConfChangedIco}
                    <div className="menu-item">
                        {localization.loraGlobalFrequency.title}
                    </div>
                </NavLink></li>

                {/* 3G configs */}
                {conf3G}

                {/* About device */}
                <li><NavLink to="/about" activeClassName="selected">
                    {availableUpdateIco}
                    <div className="menu-item">
                        {localization.about.title}
                    </div>
                </NavLink></li>

                {/* Settings */}
                <li><NavLink to="/app-settings" activeClassName="selected">
                    <div className="menu-item">
                        <i className="fa fa-wrench item-icon" aria-hidden="true"></i>
                        {localization.appSettings.title}
                    </div>
                </NavLink></li>

                {/* Device actions */}
                <li><NavLink to="/actions" activeClassName="selected">
                    <div className="menu-item">
                        <i className="fa fa-cog item-icon" aria-hidden="true"></i>
                        {localization.deviceActions.title}
                    </div>
                </NavLink></li>

            </ul>

            <div className="main-menu-mode-toggle">
                <ExpertModeToggle />
            </div>

        </div>
    )
};

export default MainMenu;
