import channelsInFrequency from '../utils/channels-in-frequency';
import { splitFrequencyData } from '../utils/split-frequency-data';
import checkDataDifferent from '../utils/check-data-different';

const initialFrequencyPresetsStore = {
    listState: 'EMPTY',
    list: [],
    presetsDataState: {
        //manual: 'READY'
    },
    presetsData: {},
    presetsDataErrors: {},
    error: null,
    selectedItem: 'manual',
    isNeedConfirm: false,
    confirmText: null,
    confirmAction: null
}

const frequencyPresetsReducer = (store, action) => {
    const { frequency_presets: frequencyPresetsStore = initialFrequencyPresetsStore } = store;
    const { presetsData = {} } = frequencyPresetsStore;
    switch (action.type) {
		case 'FETCH_FREQUENCY_PRESETS_LIST_REQUEST': {
            return {
                ...initialFrequencyPresetsStore,
                presetsData,
                listState: 'LOADING',
            }
		}
        case 'FETCH_FREQUENCY_PRESETS_LIST_SUCCESS': {
            const { payload } = action;
			return {
				...initialFrequencyPresetsStore,
                presetsData,
				listState: 'READY',
				list: payload.list,
                presetsDataState: payload.presetsDataState
			}
        }
        case 'FETCH_FREQUENCY_PRESETS_LIST_FAILURE': {
            const { payload } = action;
            return {
                ...frequencyPresetsStore,
                listState: 'ERROR',
                list: [],
                error: payload
            }
        }
        case 'FREQUENCY_PRESETS_ERROR': {
            const { payload } = action;
            return {
                ...frequencyPresetsStore,
                error: payload
            }
        }

        case 'FETCH_FREQUENCY_PRESET_REQUEST': {
            const { payload } = action;
			return {
				...frequencyPresetsStore,
				presetsData: {
                    ...presetsData,
                    [payload.presetName]: {}
                },
                presetsDataErrors: {
                    ...frequencyPresetsStore.presetsDataErrors,
                    [payload.presetName]: null
                },
                presetsDataState: {
                    ...frequencyPresetsStore.presetsDataState,
                    [payload.presetName]: 'LOADING'
                }
			}
        }

        case 'FETCH_FREQUENCY_PRESET_SUCCESS': {
            const { frequency_conf = {} } = store;
            const { selectedItem, currentDevicePreset } = frequencyPresetsStore;

            const { payload } = action;
            const deviceCurrentFrequencyChannels = channelsInFrequency({data: frequency_conf.currentData, radios: frequency_conf.currentRadios});
            const deviceFrequencyChannels = channelsInFrequency({data: frequency_conf.data, radios: frequency_conf.radios});
            const loadedFrequencyChannels = channelsInFrequency(splitFrequencyData(payload.presetData));

            const unitedDataObject = {
                currentData: deviceFrequencyChannels,
                data: loadedFrequencyChannels
            };

            const unitedCurrentDataObject = {
                currentData: deviceCurrentFrequencyChannels,
                data: loadedFrequencyChannels
            };

            const newCurrentSelectedItem = (checkDataDifferent(unitedCurrentDataObject)) ? currentDevicePreset : payload.presetName;
            const newSelectedItem = (checkDataDifferent(unitedDataObject)) ? selectedItem : payload.presetName;

			return {
				...frequencyPresetsStore,
				presetsData: {
                    ...presetsData,
                    [payload.presetName]: payload.presetData
                },
                presetsDataErrors: {
                    ...frequencyPresetsStore.presetsDataErrors,
                    [payload.presetName]: null
                },
                presetsDataState: {
                    ...frequencyPresetsStore.presetsDataState,
                    [payload.presetName]: 'READY'
                },
                selectedItem: newSelectedItem,
                currentDevicePreset: newCurrentSelectedItem || 'manual'
			}
        }

        case 'FETCH_FREQUENCY_PRESET_FAILURE': {
            const { payload } = action;
            return {
                ...frequencyPresetsStore,
                presetsData: {
                    ...presetsData,
                    [payload.presetName]: {}
                },
                presetsDataErrors: {
                    ...frequencyPresetsStore.presetsDataErrors,
                    [payload.presetName]: payload.errorText
                },
                presetsDataState: {
                    ...frequencyPresetsStore.presetsDataState,
                    [payload.presetName]: 'ERROR'
                }
            }
        }



        // TODO: was moved to  SET_PRESET_TO_FREQUENCY_CONFIG in frequency-conf-reducer
        // TODO: delete code:

        /*case 'FREQUENCY_PRESETS_SELECT_ITEM': {
            const { payload } = action;
            const { presetsDataState } = frequencyPresetsStore;

            // dont select item if item state is ERROR or something wrong
            if (payload !== 'manual') {
                if (presetsDataState[payload] !== 'EMPTY' && presetsDataState[payload] !== 'LOADING' && presetsDataState[payload] !== 'READY') {
                    return {
                        ...frequencyPresetsStore
                    };
                }
            }

            return {
                ...frequencyPresetsStore,
                selectedItem: payload
			}
        }*/

        case 'FREQUENCY_PRESETS_OPEN_CONFIRM': {
			const { payload } = action;
			const { confirmText, confirmAction, selectedItem } = payload;
			return {
				...frequencyPresetsStore,
				isNeedConfirm: true,
				confirmText,
				confirmAction,
                confirmPresetName: selectedItem,

			}
		}

		case 'FREQUENCY_PRESETS_CLOSE_CONFIRM': {
			return {
				...frequencyPresetsStore,
				isNeedConfirm: false,
				confirmText: null,
				confirmAction: null,
                confirmPresetName: frequencyPresetsStore.currentDevicePreset,
                error: null
			}
		}

        default: return frequencyPresetsStore
    }
}

export default frequencyPresetsReducer;
