import { boolValidator, numberValidator, radioNumberValidator } from './type-data-validators';

/**
* Split frequency data between radios and channels
* param {object}
*
* return {object} as {data{}, radios{}}
*/
const splitFrequencyData = (obj) => {
    const dataMask = {
        chan_multiSF_0: true,
        chan_multiSF_1: true,
        chan_multiSF_2: true,
        chan_multiSF_3: true,
        chan_multiSF_4: true,
        chan_multiSF_5: true,
        chan_multiSF_6: true,
        chan_multiSF_7: true,
    };

    const additionalChannelsMask = {
        chan_Lora_std: true,
        chan_FSK: true
    }

    const radiosMask = {
        radio_0: true,
        radio_1: true
    };

    let data = {};
	let radios = {};
    let additionalChannels = {};

    for (let key in dataMask) {
        data = {
            ...data,
            [key]: typeof obj[key] === 'object' ? obj[key] : {}
        }
    }


    for (let key in additionalChannelsMask) {
        additionalChannels = {
            ...additionalChannels,
            [key]: typeof obj[key] === 'object' ? obj[key] : {}
        }
    }

    for (let key in radiosMask) {
        radios = {
            ...radios,
            [key]: typeof obj[key] === 'object' ? obj[key] : {}
        }
    }

    return {
        data,
        additionalChannels,
        radios
    }
}

const prepareFrequencyData = ({ data: newData, radios: newRadios }) => {
    const dataTypesMask = {
        enable: 'bool',
        if: 'number',
        radio: 'radio'
    };
    const radiosTypesMask = {
        freq: 'number'
    }

    let data = {};
    let radios = {};

    for (let key in newData) {
        const newDataField = typeof newData[key] === 'object' ? newData[key] : {}
        let field = {};

        for (let fieldKey in dataTypesMask) {
            let value = null;

            switch (fieldKey) {
                case 'enable':
                    value = boolValidator(newDataField.enable);
                    break;
                case 'if':
                    value = numberValidator(newDataField.if, 9).data;
                    break;
                case 'radio':
                    value = radioNumberValidator(newDataField.radio);
                    break;
                default: value = null;
            }

            if (value !== null) {
                field = {
                    ...field,
                    [fieldKey]: value
                }
            }
        }
        data = {
            ...data,
            [key]: field
        }
    }

    for (let key in newRadios) {
        const newRadiosField = typeof newRadios[key] === 'object' ? newRadios[key] : {}
        let field = {};

        for (let fieldKey in radiosTypesMask) {
            let value = null;
            switch (fieldKey) {
                case 'freq':
                    value = numberValidator(newRadiosField.freq, 9).data;
                    break;
                default: value = null;
            }

            if (value !== null) {
                field = {
                    ...field,
                    [fieldKey]: value
                }
            }
        }

        radios = {
            ...radios,
            [key]: field
        }
    }

    return {
        data,
        radios
    }
}

export {
    splitFrequencyData,
    prepareFrequencyData
};
