import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../../utils/compose';

import { withApiService, withLocalization } from '../../../../hoc';

import {
    FrequencyExpertPresetsForm
} from '../../../../views/pages/frequency-configs/frequency-expert-presets-form';

import {
    fetchPresetFrequencyConfigs,
} from '../../../../../actions';

const FrequencyPresetsBlock = ({
    ApiService,
    localization,
    loginToken,

    list,
    error,

    presetsDataState,
    presetsData,
    presetsDataErrors,

    selectedItem,

    fetchPresetFrequencyConfigs,

    isActive,
    onSelect,
    additionalElements = []
}) => {

    const { loraGlobalFrequency } = localization;
    const { presets: presetLocalization } = loraGlobalFrequency;

    const presetElementsArray = list.map((el) => {
        const {
            name,
            description
        } = el;
        const itemLocalization = presetLocalization[name] === undefined ? {} : presetLocalization[name];
        const {
            title = name,
            description: descriptionLocalization = description
        } = itemLocalization;
        return {
            name: title,
            active: isActive,
            itemState: presetsDataState[name],
            errorText: presetsDataErrors[name],
            isSelected: name === selectedItem,
            description: descriptionLocalization,
            onSelect: ((isActive && title !== selectedItem) ? () => onSelect(title) : () => {})
        }
    })

    // Fetch data of preset items in turn
    for (let PresetName in presetsDataState) {
        if (presetsDataState[PresetName] === 'EMPTY') {
            console.log(PresetName, '= EMPTY');
            fetchPresetFrequencyConfigs(loginToken, ApiService, PresetName);
            break;
        }
        if (presetsDataState[PresetName] === 'LOADING') {
            console.log(PresetName, '= LOADING');
            break;
        }
    }

    return (
        <FrequencyExpertPresetsForm
            presetElementsArray={[
                ...presetElementsArray,
                ...additionalElements
            ]}
        />
    )
}

const mapStoreToProps = ({ loginToken, frequency_presets }) => {
    const {
        list,
        error,

        presetsDataState,
        presetsData,
        presetsDataErrors,
    } = frequency_presets;
    return {
        loginToken,

        list,
        error,

        presetsDataState,
        presetsData,
        presetsDataErrors,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchPresetFrequencyConfigs: (loginToken, ApiService, presetName) => fetchPresetFrequencyConfigs(loginToken, ApiService, presetName)(dispatch),
    }
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(FrequencyPresetsBlock);
