import React from 'react';

import './radio-switch.css';

const RadioSwitch = ({ onSelect, selected, items = [], isActive = true, styles={} }) => {
    const Items = items.map((item) => {

        const outerCircleActive = isActive ? null : {borderColor: '#aaa'};
        const innerCircleActive = !isActive && selected === (item.value) ? {backgroundColor: '#aaa'} : null;
        const labelCircleActive = isActive ? null : {color: '#555', cursor:'not-allowed'};
        const innerDisk = selected === (item.value) ? <div className={'radio-switch-inner-circle '} style={innerCircleActive}></div> : null;

        return (
            <div
                className="radio-switch-item"
                style={{...labelCircleActive, ...styles[item.name]}}
                key={item.value}
                onClick={isActive ? () => onSelect(item.value) : ()=>{}}
            >
                <div className="radio-switch-outer-circle" style={outerCircleActive}>
                    {innerDisk}
                </div>
                <div className="radio-switch-item-name">
                    {item.label}
                </div>
            </div>
        )
    });

	return (
        <div className="radio-switch">
            {Items}
        </div>
    )
};

export default RadioSwitch;
