/**
 *  Transform radio deviation of the channel to frequency.
 *
 *  @param {object}: { data, radios } , data - is channels object, radios - is radios object
 *  @return {object}: channels object
 */

const channelsInFrequency = ({ data, radios }) => {
    let inFrequencyData = {};
    for (var key in data) {
        const radioFreq = radios['radio_'+data[key].radio].freq;
        inFrequencyData = {
            ...inFrequencyData,
            [key]: {
                ...data[key],
                if: data[key].if + radioFreq
            }
        };
    }
    return inFrequencyData;
}

export default channelsInFrequency;
