import React from 'react';

import ErrorIndicator from '../../../common/error-indicator';

import './layout.css';

const AppPasswordSettingsLayout = ({ appPasswordSettingsForm, appSettingsPasswordButtons, error=null }) => {
    const err = (error !== null) ? <ErrorIndicator error={error} /> : null;
    return (
        <div className="layout app-password-settings-layout">
            {err}
            {appPasswordSettingsForm}
            {appSettingsPasswordButtons}
        </div>
    )
}

export default AppPasswordSettingsLayout;
