import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withApiService } from '../../hoc';

import { authorizationLogin } from '../../../actions';

import AuthorizationForm from '../../views/specific/authorization-form';
import Spinner from '../../views/common/spinner';

class Authorization extends Component {
    constructor() {
        super();

        this.state = {
            login: '',
            password: '',
            loading: false,
            warning: null
        }
    }

    changeLabel = (field, text) => {
        this.setState({ [field]: text });
    }

    sendData = () => {
        this.setState({ loading: true, warning: null });
        const { ApiService, authorizationLogin } = this.props;
        const { login, password } = this.state;
        const loginData = { login, password };
        const head = {
			cmd: 'login_request'
		};
        ApiService.loginRequest(head, loginData)
            .then((response) => {
                if (response.result) {
                    this.setState({ loading: false });
                    authorizationLogin(response);
                } else {
                    this.setState({
                        loading: false,
                        warning: response.msg
                    });
                }
            })
            .catch((er) => {
                console.log("ERROR - loginRequest", er);
                this.setState({
                    loading: false,
                    warning: 'cant_connect_to_server'
                });
            })
    }

    render () {
        const { login, password, loading, warning } = this.state;

        let contant = null;
        if (loading) {
            contant = <Spinner />
        } else {
            contant = <AuthorizationForm
                login={login}
                password={password}
                changeLabel={this.changeLabel}
                warning={warning}
                sendData={this.sendData}
            />
        }

        return contant;
    }
}

const mapStoreToProps = null;

const mapDispatchToProps = (dispatch) => {
	return {
        authorizationLogin: (token) => dispatch(authorizationLogin(token))
	}
}

export default connect(mapStoreToProps, mapDispatchToProps)(withApiService(Authorization));
