import React from 'react';
import { connect } from 'react-redux';

import ProtectedContent from '../containers/specific/protected-content';
import Authorization from '../containers/authorization';
import Header from '../views/specific/header';

import dictionary from '../../localization';
import { LocalizationProvider } from "../contexts/localization-context";

import UpdateSpinner from '../views/specific/update-spinner';

import './app.css';

const App = ({ language, isLogin, gatewayid, isLoadUpdate }) => {
	const localization = dictionary(language);

	const authorization = isLogin ? null : <Authorization />;
	const protectedContent = isLogin ? <ProtectedContent /> : null;
	const updateSpinner = isLoadUpdate ? <UpdateSpinner /> : null;

	return (
        <LocalizationProvider value={localization}>
			{updateSpinner}
			<div className = "container app">
				<Header
					gateway_ID={gatewayid}
					isLogin={isLogin}
					deviceName={localization.about.device.name.value}
					localization={localization}
				/>
				{authorization}
				{protectedContent}
			</div>
        </LocalizationProvider>
	)
}

const mapStoreToProps = ({ language, isLogin, gatewayid, isLoadUpdate }) => {
	return {
		language,
		isLogin,
		gatewayid,
		isLoadUpdate
	}
}

export default connect(mapStoreToProps)(App);
