import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../../utils/compose';

import { withApiService, withLocalization } from '../../../../hoc';

import MainPage from '../../../../views/common/main-page';
import Spinner from '../../../../views/common/spinner';
import ErrorIndicator from '../../../../views/common/error-indicator';
import ConfirmForm from '../../../../views/common/confirm-form';
import {
    FrequencyPresetsFormButtons
} from '../../../../views/pages/frequency-configs/frequency-presets-form';

import FrequencyPresetsBlock from '../frequency-presets-block';

import {
    fetchFrequencyConfigs,
    fetchFrequencyPresets,
    setFrequencyPreset,
    frequencyPresetsOpenConfirm,
    frequencyPresetsCloseConfirm
} from '../../../../../actions';

const FrequencyPresetsPage = ({
    ApiService,
    localization,
    loginToken,

    listState,
    error,
    dataState,
    frequencyError,

    selectedItem,
    currentDevicePreset,

    isNeedConfirm,
    confirmText,
    confirmAction,

    fetchFrequencyConfigs,
    fetchFrequencyPresets,
    setFrequencyPreset,

    frequencyPresetsOpenConfirm,
    frequencyPresetsCloseConfirm,
}) => {
    const { loraGlobalFrequency } = localization;

    const reRequestPresetsListConfirmData = {
		confirmText: loraGlobalFrequency.confirmReqests.onReRequestPresetList,
		confirmAction: ()=>fetchFrequencyPresets(loginToken, ApiService)
	}

    const onSelect = (presetName) => {
        const setPresetConfirmData = {
            confirmText: loraGlobalFrequency.confirmReqests.onSavePreset,
            confirmAction: () => setFrequencyPreset(loginToken, ApiService, presetName),
            selectedItem: presetName
        }
        frequencyPresetsOpenConfirm(setPresetConfirmData);
    }

    const { presets: presetLocalization } = loraGlobalFrequency;

    const manualElement = currentDevicePreset !== 'manual' ? undefined : [{
        name: presetLocalization.manual.title,
        active: !isNeedConfirm,
        isSelected: (isNeedConfirm ? false : currentDevicePreset === 'manual'),
        description: presetLocalization.manual.description,
        itemState: 'READY',
        onSelect: () => {},
        isAuxiliary: true
    }];

    let content;
    switch (listState) {
        case 'EMPTY': {
            fetchFrequencyPresets(loginToken, ApiService);
            break;
        }
        case 'READY': {
            switch (dataState) {
                case 'EMPTY': {
                    fetchFrequencyConfigs(loginToken, ApiService);
                    break;
                }
                case 'READY': {
                    content = <FrequencyPresetsBlock
                        additionalElements={manualElement}
                        isActive={!isNeedConfirm}
                        onSelect={(i) => onSelect(i)}
                        selectedItem={(isNeedConfirm ? selectedItem : currentDevicePreset)}
                    />
                    break;
                }
                case 'LOADING': content = <Spinner />
                    break;
                case 'ERROR': content = <ErrorIndicator error={frequencyError}/>
                    break;
                default: content = null;
            }
            break;
        }
        case 'LOADING': content = <Spinner />
            break;
        case 'ERROR': content = <ErrorIndicator error={error}/>
            break;
        default: content = null;
    }

    let buttons = null;
    if (isNeedConfirm) {
        buttons = <ConfirmForm
            confirmText={confirmText}
            onConfirm={confirmAction}
            onCancel={()=>frequencyPresetsCloseConfirm()}
        />
    } else {
        buttons = <FrequencyPresetsFormButtons
            dataState={listState}
            onReRequestData={()=>frequencyPresetsOpenConfirm(reRequestPresetsListConfirmData)}
            localization={localization}
        />;
    }

    return (
        <MainPage
            name={loraGlobalFrequency.title}
            content={content}
            buttons={buttons}
            error={listState !== 'ERROR' ? error : null}
        />
    )
}

const mapStoreToProps = ({ loginToken, frequency_conf, frequency_presets }) => {
    const {
        listState,
        error,

        isNeedConfirm,
        confirmText,
        confirmAction,
        confirmPresetName: selectedItem,
        currentDevicePreset
    } = frequency_presets;
    const {
        dataState,
        error: frequencyError
    } = frequency_conf;
    return {
        loginToken,

        listState,
        error,
        dataState,
        frequencyError,

        isNeedConfirm,
        confirmText,
        confirmAction,
        selectedItem,
        currentDevicePreset
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchFrequencyConfigs: (loginToken, ApiService) => fetchFrequencyConfigs(loginToken, ApiService)(dispatch),
        fetchFrequencyPresets: (loginToken, ApiService) => fetchFrequencyPresets(loginToken, ApiService)(dispatch),

        setFrequencyPreset: (loginToken, ApiService, presetName) => setFrequencyPreset(loginToken, ApiService, presetName)(dispatch),

        frequencyPresetsOpenConfirm: (payload) => dispatch(frequencyPresetsOpenConfirm(payload)),
        frequencyPresetsCloseConfirm: () => dispatch(frequencyPresetsCloseConfirm())
    }
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(FrequencyPresetsPage);
