import React from 'react';

import { withLocalization } from '../../../hoc';

import './logout-form.css';

const LogoutForm = ({ onLogout, localization }) => {
    return (
        <div className='logout-form right'>
            <button className="button" onClick={onLogout}>
                {localization.logOut}<i className="fa fa-sign-out" aria-hidden="true"></i>
            </button>
        </div>
    )
}

export default withLocalization(LogoutForm);
