import React from 'react';

import ErrorIndicator from '../../../common/error-indicator';

import './app-settings-layout.css';

const Layout = ({ appSettingsManagerPanel, appSettingsManagerButtons, error }) => {
    const err = (error !== null) ? <ErrorIndicator error={error} /> : null;
    return (
        <div className="layout manager-settings-layout">
            {err}
            {appSettingsManagerPanel}
            {appSettingsManagerButtons}
        </div>
    )
}

export default Layout;
