import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';
import {
	networkConfigsEdit,
	fetchNetworkConfigs,
	networkOpenConfirm,
	networkCloseConfirm,
	sendNetworkConfigs
} from '../../../../actions';
import { isObjectValid } from '../../../../utils/page-data-validators';

import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';
import ConfirmForm from '../../../views/common/confirm-form';
import { NetworkConfForm, NetworkConfFormButtons } from '../../../views/pages/network';
import MainPage from '../../../views/common/main-page';

const NetworkConfPage = ({
    loginToken,
	expertMode,
    dataState,
    data,
	currentData,
    isDataValid,
	isNeedConfirm,
	confirmText,
	confirmAction,
    error,

    fetchNetworkConfigs, //()    ()- 
	networkConfigsEdit, //() 
	sendNetworkConfigs, //()    ()- 
	networkOpenConfirm, //{ confirmText, confirmAction }
	networkCloseConfirm,

    ApiService,
    localization
}) => {
	const { deviceNetwork } = localization;

	const onChangeLabel = (field, value) => {
		if (!isNeedConfirm) {
			networkConfigsEdit(data, currentData, field, value);
		}
	}

	const onToggleCheckbox = (field) => {
		if (!isNeedConfirm) {
			let value = '';
			switch (field) {
				case 'eth0':
					value = data[field] === 'static' ? 'dhcp' : 'static';
					break;
				default:
			}
			networkConfigsEdit(data, currentData, field, value);
		}
	}

	const setConfigs = () => {
		const isValid = isObjectValid(isDataValid);
		if (isValid) {
			networkOpenConfirm(setConfigsConfirmData)
		} else {
            alert(localization.confirm.notValidFields);
        }
	}

	const setConfigsConfirmData = {
		confirmText: deviceNetwork.confirmReqests.onSave,
		confirmAction: () => sendNetworkConfigs(data, isDataValid, loginToken, ApiService)
	}
	const reRequestData = {
		confirmText: deviceNetwork.confirmReqests.onReRequest,
		confirmAction: () => fetchNetworkConfigs(loginToken, ApiService)
	}

	if (dataState === 'EMPTY') {
		fetchNetworkConfigs(loginToken, ApiService);
	}

	let content;
	switch (dataState) {
		case 'READY':
			content = <NetworkConfForm
				isExpertMode={expertMode}
				data={data}
				currentData={currentData}
				isDataValid={isDataValid}
				onChangeLabel={onChangeLabel}
				onToggleCheckbox={onToggleCheckbox}
				active={!isNeedConfirm}
				localization={localization}
				onSubmit={setConfigs}
				/>
			break;
		case 'LOADING': content = <Spinner />
			break;
		case 'ERROR': content = <ErrorIndicator error={error}/>
			break;
		default: content = null;
	}

	let buttons = null;
	if (isNeedConfirm) {
		buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>networkCloseConfirm()}/>
	} else {
		buttons = <NetworkConfFormButtons
			dataState={dataState}
			setConfigs={setConfigs}
			onReRequestData={()=>networkOpenConfirm(reRequestData)}
			localization={localization}
		/>
	}

	return (
		<MainPage name={deviceNetwork.title} content={content} buttons={buttons} error={dataState !== 'ERROR' ? error : null} />
	)
}

const mapStoreToProps = ({ loginToken, network_conf, expertMode }) => {
    const {
		dataState,
		data,
		currentData,
		isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,
		error
	} = network_conf;
    return {
        loginToken,
		expertMode,
        dataState,
        data,
		currentData,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    }
}

const mapDispatchToProps = (dispatch) => {
	return {
		fetchNetworkConfigs: (loginToken, ApiService) => fetchNetworkConfigs(loginToken, ApiService)(dispatch),
		sendNetworkConfigs: (data, isDataValid, loginToken, ApiService) => sendNetworkConfigs(data, isDataValid, loginToken, ApiService)(dispatch),
		networkConfigsEdit: (data, currentData, field, value) => dispatch(networkConfigsEdit(data, currentData, field, value)),
		networkOpenConfirm: (payload) => dispatch(networkOpenConfirm(payload)), // { confirmText, confirmAction }
		networkCloseConfirm: () => dispatch(networkCloseConfirm())
	}
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(NetworkConfPage);
