import React from 'react';

import './light-text-label.css';

const LightTextLabel = ({
    value,
    onChange,
    description = null,
    isValid = true,
    notValidDescription = null,
    isActive,
    onSubmit,
    password = false
}) => {
    const onEnter = (event) => {
        if(event.key === 'Enter' && isActive) {
            onSubmit();
        };
    }

    const type = password ? 'password' : 'text';

    const disabled = isActive ? null : {disabled: 'disabled'};
    const divStyle = isActive ? null : {cursor:'not-allowed', color:'#666'};
    const inputStyle = isActive ? null : {backgroundColor: '#eee', cursor:'not-allowed'};

    const inputValidStyle = (isValid && notValidDescription === null) ? null: {boxShadow:'0 0 2px 1px #f55', borderColor: '#f00'};
    const warningItem = notValidDescription === null ? null : <WarningItem text={notValidDescription}  />;
    const descriptionItem = description === null ? null : <DescriptionItem text={description}  />;


    return (
        <div className="light-text-label" style={divStyle}>
            <input
                {...disabled}
                style={{...inputStyle, ...inputValidStyle}}
                type={type}
                value={value}
                onChange={(item) => onChange(item.target.value)}
                onKeyPress={onEnter}
            />

            {descriptionItem}

            {warningItem}

        </div>
    )
}

export default LightTextLabel;

const DescriptionItem = ({text}) => {
    return (
        <span className="dropdown dropdown-description">
            <div className="dropdown-menu">
                <div>
                    {text}
                </div>
            </div>
            <button className="dropdown-toggle info-circle" data-toggle="dropdown">
                <i className="fa fa-info-circle" aria-hidden="true"></i>
            </button>
        </span>
    )
}

const WarningItem = ({text}) => {
    return (
        <span className="dropdown dropdown-warning">
            <div className="dropdown-menu">
                <div>
                    {text}
                </div>
            </div>
            <button className="dropdown-toggle info-circle" data-toggle="dropdown">
                <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
            </button>
        </span>
    )
}
