import { clearFrequencyConfigs } from './index';

const frequencyPresetsRequested = () => {
	return {
		type: 'FETCH_FREQUENCY_PRESETS_LIST_REQUEST'
	}
}

const frequencyPresetsLoaded = (payload) => {
	let presetsDataState = {};
	for(let i in payload) {
		presetsDataState = {
			...presetsDataState,
			[payload[i].name]: 'EMPTY'
		}
	}
	return {
		type: 'FETCH_FREQUENCY_PRESETS_LIST_SUCCESS',
		payload: {
			list: payload,
			presetsDataState
		}
	}
}

const frequencyPresetsFailure = (payload) => {
	return {
		type: 'FETCH_FREQUENCY_PRESETS_LIST_FAILURE',
		payload
	}
}

const frequencyPresetsError = (payload) => {
	return {
		type: 'FREQUENCY_PRESETS_ERROR',
		payload
	}
}

const frequencyPresetsOpenConfirm = (payload) => {
	return {
		type: 'FREQUENCY_PRESETS_OPEN_CONFIRM',
        payload
	}
}

const frequencyPresetsCloseConfirm = () => {
	return {
		type: 'FREQUENCY_PRESETS_CLOSE_CONFIRM'
	}
}

const fetchFrequencyPresets = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'get_freq_preset_list',
        loginToken
    };
    dispatch(frequencyPresetsRequested());
    ApiService.frequencyPresetsRequest(head)
        .then((response) => {
            if (response.result) {
                dispatch(frequencyPresetsLoaded(response.data))
            } else {
                dispatch(frequencyPresetsFailure(response.msg));
            }
        })
        .catch((er) => {
            console.error("ERROR", er);
            dispatch(frequencyPresetsFailure('cant_connect_to_server'));
        });
}

const setFrequencyPreset = (loginToken, ApiService, presetName) => (dispatch) => {
    const body = {
		presetName
	}
	const head = {
		cmd: 'set_freq_preset',
		loginToken
	};
	dispatch(frequencyPresetsCloseConfirm());

	ApiService.frequencyPresetsRequest(head, body)
		.then((response) => {
			if (response.result) {
				dispatch(clearFrequencyConfigs());
			} else {
				dispatch(frequencyPresetsError(response.msg));
			}
		})
		.catch((er) => {
			console.log("ERROR - setFrequencyPreset", er);
			dispatch(frequencyPresetsError('cant_connect_to_server'));
		});
}

export {
    fetchFrequencyPresets,
    setFrequencyPreset,

    frequencyPresetsOpenConfirm,
    frequencyPresetsCloseConfirm
}
