import React from 'react';

import { withLocalization } from '../../../hoc';

import AppSettingsLayout from '../../../views/pages/app-settings/layout';

import AppDashboardSettings from '../app-dashboard-settings';
import AppManagerSettings from '../app-manager-settings';
import AppPasswordSettings from '../app-password-settings';

const AppSettings = ({ localization }) => {
    const { appSettings: appSettingsLocal={} } = localization;
    return(
        <AppSettingsLayout
            localization={appSettingsLocal}
            appManagerSettings={<AppManagerSettings />}
            appDashboardSettings={<AppDashboardSettings />}
            appPasswordSettings={<AppPasswordSettings />}
            title={localization.appSettings.title}
        />
    )
}

export default withLocalization(AppSettings);
