const mainConfigsReducer = (store, action) => {
    switch (action.type) {
        case 'SET_LANGUAGE': {
            const { payload } = action;
            return {
                language: payload
            }
        }
        case 'FETCH_DEVICE_LANGUAGE_LOADING': {
            return {
                langFromDeviceState: 'LOADING'
            }
        }
        case 'FETCH_DEVICE_LANGUAGE_SUCCESS': {
            const { payload } = action;
            return {
                langFromDeviceState: 'READY',
                language: payload
            }
        }
        case 'FETCH_DEVICE_LANGUAGE_FAILURE': {
            return {
                langFromDeviceState: 'ERROR'
            }
        }
        case 'TOGGLE_EXPERT_MODE': {
            const { expertMode } = store;
            return {
                expertMode: !expertMode
            }
        }
        case 'DROP_ALL_SETTINGS_DATA': {
            return {
                about: {
                    ...store.about,
                    aboutState: 'EMPTY'
                },
                conf_3G: {
                    ...store.conf_3G,
                    dataState: 'EMPTY'
                },
                frequency_conf: {
                    ...store.frequency_conf,
                    dataState: 'EMPTY'
                },
                frequency_presets: {
                    ...store.frequency_presets,
                    listState: 'EMPTY'
                },
                global_conf: {
                    ...store.global_conf,
                    dataState: 'EMPTY'
                },
                network_conf: {
                    ...store.network_conf,
                    dataState: 'EMPTY'
                }
            }
        }
        case 'DEVICE_ACTIONS_ERROR': {
            const { payload } = action;
            return {
                deviceActionsError: payload
            }
        }
        case 'SHOW_UPDATE_LOADING': {
            return {
                isLoadUpdate: true
            }
        }
        case 'HIDE_UPDATE_LOADING': {
            return {
                isLoadUpdate: false
            }
        }
        case 'MAIN_OPEN_CONFIRM': {
            const { payload } = action;
            const { confirmText, confirmAction } = payload;
            return {
                deviceActionsError: null,
                isNeedConfirm: true,
                confirmText,
                confirmAction
            }
        }
        case 'MAIN_CLOSE_CONFIRM': {
            return {
                isNeedConfirm: false,
                confirmText: null,
                confirmAction: null
            }
        }
        default: return {};
    }
}

export default mainConfigsReducer;
