import { deepMap } from '../utils/objects-utils/deep-map-obj';
import { networkConfValidator, isObjectValid } from '../utils/page-data-validators';

// TODO: create action
const clearNetworkConfigs = () => {
	return {
		type: 'CLEAR_NETWORK_CONFIGS'
	}
}

const networkConfigsRequested = () => {
	return {
		type: 'FETCH_NETWORK_CONFIGS_REQUEST'
	}
}

const networkConfigsLoaded = (data) => {
	const isValid = deepMap(data, (key, val) => {
		const result = networkConfValidator(key, val).isValid;
		return (result[key]);
	});
	const payload = {
		data,
		isValid
	}
	return {
		type: 'FETCH_NETWORK_CONFIGS_SUCCESS',
		payload
	}
}

const networkConfigsFailure = (payload) => {
	return {
		type: 'FETCH_NETWORK_CONFIGS_FAILURE',
		payload
	}
}

const networkConfigsError = (payload) => {
	return {
		type: 'NETWORK_CONFIGS_ERROR',
		payload
	}
}

const networkConfigsEdit = (data, currentData, field, value) => {
	const payload = networkConfValidator(field, value);
	return {
		type: 'EDIT_NETWORK_CONFIGS',
		payload
	}
}

const networkConfigsSaved = () => {
	return {
		type: 'SAVED_NETWORK_CONFIGS'
	}
}

const networkOpenConfirm = (payload) => {
	return {
		type: 'NETWORK_OPEN_CONFIRM',
        payload
	}
}

const networkCloseConfirm = () => {
	return {
		type: 'NETWORK_CLOSE_CONFIRM'
	}
}

const sendNetworkConfigs = (data, isDataValid, loginToken, ApiService) => (dispatch) => {
	const head = {
		cmd: 'set_network_conf',
		loginToken
	};
	if(isObjectValid(isDataValid)) {
		dispatch(networkCloseConfirm());

		ApiService.networkConfigsRequest(head, data)
			.then((response) => {
				if (response.result) {
					fetchNetworkConfigs(loginToken, ApiService)(dispatch);
				} else {
					dispatch(networkConfigsError(response.msg));
				}
			})
			.catch((er) => {
				console.log("ERROR - sendConfigs", er);
	            dispatch(networkConfigsError('cant_connect_to_server'));
			});
	} else {
		console.log('isDataValid = false');
	}
}

const fetchNetworkConfigs = (loginToken, ApiService) => (dispatch) => {
	    const head = {
        cmd: 'get_network_conf',
        loginToken
    };
    dispatch(networkConfigsRequested());
    ApiService.networkConfigsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(networkConfigsLoaded(response.data))
			} else {
				dispatch(networkConfigsFailure(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(networkConfigsFailure('cant_connect_to_server'));
        });
}


export {
	clearNetworkConfigs,
	networkConfigsError,
	networkConfigsEdit,
	networkConfigsSaved,
    networkOpenConfirm,
    networkCloseConfirm,

    fetchNetworkConfigs,
	sendNetworkConfigs
}
