import React from 'react';

import { connect } from 'react-redux';
import { withApiService } from '../../../hoc';

import {
	fetchGlobalConfigs,
	fetchAboutInfo,
	authorizationLogout
} from '../../../../actions';

import ProtectedContentView from '../../../views/specific/protected-content-view';


import './protected-content.css';

const ProtectedContent = ({
	ApiService,
	loginToken,
	expertMode,
	dataState,
	aboutState,
	fetchGlobalConfigs,
	fetchAboutInfo,
	authorizationLogout
 }) => {

	if (dataState === 'EMPTY') {
		fetchGlobalConfigs(loginToken, ApiService);
	} else if (dataState === 'READY' && aboutState === 'EMPTY') {
		fetchAboutInfo(loginToken, ApiService);
	}

	return (
		<ProtectedContentView expertMode={expertMode}/>
	)
}

const mapStoreToProps = ({ loginToken, global_conf, expertMode, aboutState }) => {
	const { dataState } = global_conf;
	return {
		loginToken,
		expertMode,
		dataState,
		aboutState
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		fetchGlobalConfigs: (loginToken, ApiService) => fetchGlobalConfigs(loginToken, ApiService)(dispatch),
		fetchAboutInfo: (loginToken, ApiService) => fetchAboutInfo(loginToken, ApiService)(dispatch),
		authorizationLogout: () => dispatch(authorizationLogout())
	}
}

export default connect(mapStoreToProps, mapDispatchToProps)(withApiService(ProtectedContent));
