/*
*   @param [String, number] data
*/
const boolValidator = (data) => {
    return data ? true : false;
}

const portValidator = (data) => {
    let text = '' + data;
    let result = text.substr(0, 5);
    result = Number.parseInt(result);
    result = isNaN(result) ? 0 : result;
    result = result > 65535 ? 65535 : result;
    return {data: result, isValid: true};
}

const ipValidator = (text) => {
    let workString = '' + text;
    let result = '';
    let ipArray = [];
    for (let i = 0; i < 4; i++) {
        let indexOfDot = workString.indexOf('.');
        let substrBeforeDot = workString;
        if (indexOfDot > -1) {
            substrBeforeDot = workString.substring(0, indexOfDot);
            workString = workString.substring(indexOfDot + 1);
        }
        substrBeforeDot = substrBeforeDot.substring(0, 4);  //468512 => 4685
        let number = Number.parseInt(substrBeforeDot);
        const stringNumber = isNaN(number) ? '' : '' + number;
        number = isNaN(number) ? 0 : number;
        if(number > 255) {
            let cutPosition = (Number.parseInt(stringNumber.substring(0, stringNumber.length-1)) > 255) ? stringNumber.length-2 : stringNumber.length-1;

            if(i === 3) {
                ipArray[i] = stringNumber.substring(0, cutPosition);
            } else {
                ipArray[i] = stringNumber.substring(0, cutPosition);
                i++;
                ipArray[i] = stringNumber.substring(cutPosition);
            }
        } else {
            ipArray[i] = stringNumber;
        }

        if (indexOfDot < 0) {
            break;
        }
    }

    let isValid = ipArray.length === 4 ? true : false;
    for (var i = 0; i < ipArray.length; i++) {
        result += (i !== 0 ? '.' : '') + ipArray[i];
        if (isNaN(Number.parseInt(ipArray[i]))) {
            isValid = false;
        }
    }

    return {data: result, isValid};
}

const ipWithPortValidator = (text) => {
    const indexOfDoubleDot = text.indexOf(':');
    const ipText = indexOfDoubleDot > 0 ? text.substring(0, indexOfDoubleDot) : text;
    const portText = indexOfDoubleDot > 0 ? text.substring(indexOfDoubleDot + 1) : null;

    const ipValidResult = ipValidator(ipText);
    if (ipValidResult.data !== ipText || !ipValidResult.isValid) {
        return {data: text, isValid: false};
    }
    if (indexOfDoubleDot > 0) {
        const portValidResult = portValidator(portText);
        if (('' + portValidResult.data) !== portText || !portValidResult.isValid) {
            return {data: text, isValid: false};
        }
    }
    return {data: text, isValid: true};
}

const netMaskValidator = (str) => {
    const binar = (number) => {
    	let binarString = number.toString(2)
        const length = binarString.length;
    	for (var i = 0; i < 8 - length; i += 1) {
    		binarString = "0" + binarString;
    	}
    	return binarString;
    }

    const maskArrayString = str.split(".");
    if (maskArrayString.length !== 4) {
        return false;
    }

    let mask = '';
    for (var i = 0; i < maskArrayString.length; i++) {
        const number = Number.parseInt(maskArrayString[i]);
        if (isNaN(number)) {
            return false;
        } else if (number < 0 || number > 255) {
            return false;
        } else {
            mask += binar(number)
        }
    }

    // TODO: delete console.log();
    //console.log('mask array =', maskArrayString);
    //console.log('mask =', mask);

    let maskState = '1';
    for (var j = 0; j < mask.length; j++) {
        const currentMask = mask.substr(j, 1);
        switch (maskState) {
            case '1': {
                if (currentMask === '1') {

                } else if (currentMask === '0') {
                    maskState = '0';
                } else {
                    return false;
                }
                break;
            }
            case '0': {
                if (currentMask === '1') {
                    return false;
                } else if (currentMask === '0') {

                } else {
                    return false;
                }
                break;
            }
            default: {
                return false;
            }
        }
    }

    if (maskState === '1') {
        return false;
    }

    return true;
}

const eth0Validator = (str) => {
    return {
        data: str,
        isValid: (str === 'dhcp' || str === 'static')
    }
}

const domainAdressValidator = (str) => {
    let result = str;
    result = result.substr(0, 63);
    const re = /^(http[s]?:\/\/)?([a-z0-9]{1,1}[a-z0-9-]{0,63}[a-z0-9]{1,1}\.){1,4}[a-z]{2,4}(:[0-9]{1,5})?(\/[a-z0-9]{1,1}[a-z0-9-]{0,63}[a-z0-9]{1,1}){0,7}$/i;
    const isValid = re.test(result);
    return {data: result, isValid};
}

const domainAdressWithoutProtocolValidator = (str) => {
    let result = str;
    result = result.substr(0, 63);
    const re = /^([a-z0-9]{1,1}[a-z0-9-]{0,63}[a-z0-9]{1,1}\.){1,4}[a-z]{2,4}(:[0-9]{1,5})?(\/[a-z0-9]{1,1}[a-z0-9-]{0,63}[a-z0-9]{1,1}){0,7}$/i;
    const isValid = re.test(result);
    return {data: result, isValid};
}

const partOfUrlPathValidator =  (str) => {
    let result = str;
    result = result.substr(0, 63);
    const re = /^[a-z0-9]{1,1}[a-z0-9-]{0,63}[a-z0-9]{1,1}$/i;
    const isValid = re.test(result);
    return {data: result, isValid};
}

const providerPhoneValidator = (str) => {
    let result = str.substr(0, 16);
    const re = /^[*]{1,1}[0-9*#]+[#]{1,1}$/;
    const isValid = re.test(result);
    return {data: result, isValid};
}

const namePassValidator = (str) => {
    let result = str.substr(0, 16);
    const re = /^[a-z0-9]{1,16}$/i; //  i -  
    const isValid = re.test(result);
    return {data: result, isValid};
}

const numberValidator = (numStr, maxLen) => {
    let text = '' + numStr;
    text = text.split(" ").join("");
    let result = text.substr(0, maxLen);
    result = Number.parseInt(result);
    result = isNaN(result) ? 0 : result;
    return {data: result, isValid: true};
}

const radioNumberValidator = (num) => {
    return (num === 1 || num === '1') ? 1 : 0;
}

export {
    boolValidator,
    portValidator,
    ipValidator,
    ipWithPortValidator,
    netMaskValidator,
    eth0Validator,
    domainAdressValidator,
    domainAdressWithoutProtocolValidator,
    partOfUrlPathValidator,
    providerPhoneValidator,
    namePassValidator,
    numberValidator,
    radioNumberValidator
}
