const setLanguage = (payload) => {
    return {
        type: 'SET_LANGUAGE',
        payload
    }
}

const fetchDeviceLanguageRequested = () => {
    return {
        type: 'FETCH_DEVICE_LANGUAGE_LOADING'
    }
}

const fetchDeviceLanguageLoaded = (payload) => {
    return {
        type: 'FETCH_DEVICE_LANGUAGE_SUCCESS',
        payload
    }
}

const fetchDeviceLanguageFailure = () => {
    return {
        type: 'FETCH_DEVICE_LANGUAGE_FAILURE'
    }
}

const toggleExpertMode = () => {
    return {
        type: 'TOGGLE_EXPERT_MODE'
    }
}

const fetchDeviceLanguage = (ApiService) => (dispatch) => {
    const head = {
        cmd: 'fetch_language',
    }
    const data = {};

    dispatch(fetchDeviceLanguageRequested());

    ApiService.initialRequest(head, data).then((response) => {
        if (response.result) {
            dispatch(fetchDeviceLanguageLoaded(response.data));
        } else {
            console.log(response.msg);
            dispatch(fetchDeviceLanguageFailure());
        }
    }).catch((er) => {
        console.log(er);
        dispatch(fetchDeviceLanguageFailure());
    })
}

const setDeviceLanguage = (ApiService, language) => (dispatch) => {
    const head = {
        cmd: 'set_language',
    }
    const data = language;

    dispatch(setLanguage(language));

    ApiService.initialRequest(head, data).then((response) => {
        if (response.result) {
            console.log('Language was set to device.');
        } else {
            console.log(response.msg);
        }
    }).catch((er) => {
        console.log(er);
    })
}

export {
    fetchDeviceLanguage,
    setDeviceLanguage,
    toggleExpertMode
};
