import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';
import { isObjectValid } from '../../../../utils/page-data-validators';

import {
    fetchManagerAppSettings,
    sendManagerAppSettings,
    editManagerAppSettings,
    managerAppSettingsOpenConfirm,
    managerAppSettingsCloseConfirm
} from '../../../../actions';

import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';
import ConfirmForm from '../../../views/common/confirm-form';

import AppSettingsLayout from '../../../views/pages/app-manager-settings/layout';
import AppSettingsManagerPanel from '../../../views/pages/app-manager-settings/panel';
import AppSettingsManagerButtons from '../../../views/pages/app-manager-settings/buttons';

const AppSettings = ({
    loginToken,
    appSettings,
    fetchManagerAppSettings,
    sendManagerAppSettings,
    editManagerAppSettings,
    managerAppSettingsOpenConfirm,
    managerAppSettingsCloseConfirm,

    localization,
    ApiService
}) => {
    const { appSettings: appSettingsLocal={} } = localization;
    const { appManager: appManagerLocal={} } = appSettingsLocal;

    const {
        managerSettings = {}
    } = appSettings;

    const sendConfigsConfirmData = {
		confirmText: appManagerLocal.confirmReqests.set,
		confirmAction: () => sendManagerAppSettings({loginToken, ApiService, data: managerSettings.data})
	}
    const getConfigsConfirmData = {
		confirmText: appManagerLocal.confirmReqests.get,
		confirmAction: () => fetchManagerAppSettings({loginToken, ApiService})
	}

    const sendData = (confirmerData, isValid) => () => {
        if (isObjectValid(isValid)) {
            managerAppSettingsOpenConfirm(confirmerData);
        } else {
            alert(localization.confirm.notValidFields);
        }
    }

    const onChangeManager = (part) => (field) => (val) => {
        editManagerAppSettings({part, field, val});
    }

    let appSettingsManagerPanel = null;
    switch (managerSettings.dataState) {
        case 'EMPTY': {
            fetchManagerAppSettings({loginToken, ApiService});
            break;
        }
        case 'LOADING': {
            appSettingsManagerPanel = <Spinner />
            break;
        }
        case 'SUCCESS': {
            appSettingsManagerPanel = <AppSettingsManagerPanel
                isActive={!managerSettings.isNeedConfirm}
                data={managerSettings.data}
                isValid={managerSettings.isValid}
                onChange={onChangeManager}
                onSubmit={sendData(sendConfigsConfirmData, managerSettings.isValid)}
                localization={appManagerLocal}
                />;
            break;
        }
        case 'ERROR': {
            appSettingsManagerPanel = <ErrorIndicator error={managerSettings.error} />
            break;
        }
        default: {}
    }

    let appSettingsManagerButtons = null;
    if (managerSettings.isNeedConfirm) {
        appSettingsManagerButtons = <ConfirmForm
            confirmText={managerSettings.confirmText}
            onConfirm={managerSettings.confirmAction}
            onCancel={()=>managerAppSettingsCloseConfirm()}
        />
    } else {
        appSettingsManagerButtons = <AppSettingsManagerButtons
            rerequestData={()=>managerAppSettingsOpenConfirm(getConfigsConfirmData)}
            sendData={sendData(sendConfigsConfirmData, managerSettings.isValid)}
            localization={appManagerLocal}
        />
    }

    return (
        <AppSettingsLayout
            error={managerSettings.dataState !== 'ERROR' ? managerSettings.error : null}
            appSettingsManagerPanel={appSettingsManagerPanel}
            appSettingsManagerButtons={appSettingsManagerButtons}
            title={'settings'}
        />
    )
}

const mapStoreToProps = ({ loginToken, appSettings }) => {
    return {
        loginToken,
        appSettings
    }
}

const mapDispatchToProps = {
    fetchManagerAppSettings,
    sendManagerAppSettings,
    editManagerAppSettings,
    managerAppSettingsOpenConfirm,
    managerAppSettingsCloseConfirm
}

export default compose(
    withApiService,
    withLocalization,
    connect(mapStoreToProps, mapDispatchToProps)
)(AppSettings);
