const checkDataDifferent = ({currentData, data}) => {
    let result = false;
    const deepObj = (object1, object2) => {
        for(var prop in object1) {
            if (typeof object1[prop] === 'object') {
                deepObj(object1[prop], object2[prop]);
            } else {
                if (object1[prop] !== object2[prop]) {
                    result = true;
                    break;
                }
            }
        }
        return result;
    }
     return deepObj(currentData, data);
}

export default checkDataDifferent;
