import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';

import {
    fetchAboutInfo,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm
} from '../../../../actions';

import { AboutView } from '../../../views/pages/about';
import ConfirmForm from '../../../views/common/confirm-form';
import MainPage from '../../../views/common/main-page';
import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';

const AboutPage = ({
    loginToken,
    expertMode,
    isSupport3G,
    gatewayid = '---',
    aboutState,
    aboutError,
    about,
    isNeedConfirm ,confirmText, confirmAction,

    fetchAboutInfo,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm,

    ApiService,
    localization
}) => {
    if (aboutState === 'EMPTY') {
        fetchAboutInfo(loginToken, ApiService);
    };

    const updateServerConfirmData = {
        confirmText: localization.about.confirmReqests.onUpdate,
		confirmAction: () => updateServerAction(loginToken, ApiService, localization)
    };

    let content;
    switch (aboutState) {
        case 'READY': {
            content = <AboutView
                about={about}
                gatewayid={gatewayid}
                isSupport3G={isSupport3G}
                localization={localization}
                onUpdate={()=>mainOpenConfirm(updateServerConfirmData)}
            />;
            break;
        }
        case 'LOADING': content = <Spinner />
            break;
        case 'ERROR': content = <ErrorIndicator error={aboutError}/>
            break;
        default: {
            content = null
        }
    }

    let buttons = null;
    if (isNeedConfirm) {
        buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>mainCloseConfirm()}/>
    };

    return (
        <MainPage
            name={localization.about.title}
            content={content}
            buttons={buttons}
        />
    )
}

const mapStoreToProps = ({ loginToken, isSupport3G, aboutState, aboutError, about, gatewayid, expertMode,isNeedConfirm ,confirmText, confirmAction }) => {
    return {
        loginToken,
        expertMode,
        isSupport3G,
        aboutState,
        aboutError,
        gatewayid,
        about,
        isNeedConfirm ,confirmText, confirmAction
    }
}

const mapDispatchToProps = {
    fetchAboutInfo,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(AboutPage);
