import React, { Component } from 'react';

import './page-spoiler.css';

class PageSpoiler extends Component {

    constructor() {
        super();

        this.state = {ariaExpanded: 'false'};
    }

    toggleAriaExpanded = (ariaExpanded) => {
        this.setState(() => {
            return {
                ariaExpanded: ariaExpanded
            }
        })
    }

    render () {
        const {title, children, uniqName} = this.props;
        return (
            <div className="page-spoiler">
                <div className="page-spoiler-title">
                    <div
                        className="page-spoiler-button"
                        data-toggle="collapse"
                        href={"#collapsePageSpoiler"+uniqName}
                        aria-expanded="false"
                        aria-controls={"collapsePageSpoiler"+uniqName}
                        onClick={(e)=> {
                            this.toggleAriaExpanded(document.getElementById("collapsePageSpoiler"+uniqName).getAttribute('aria-expanded'))
                        }}
                    >

                        <div className="page-spoiler-line"></div>

                        <div className="page-spoiler-button-text">
                            {title}{this.state.ariaExpanded === 'true' ? <i className="fa fa-caret-up" aria-hidden="true"></i> : <i className="fa fa-caret-down" aria-hidden="true"></i>}
                        </div>

                    </div>
                </div>
                <div className="collapse" id={"collapsePageSpoiler"+uniqName}>
                   <div className="inner-menu">
                       {children}
                   </div>
                </div>
            </div>
        )
    }
};

export default PageSpoiler;
