import mainAppConfigsReducer from './main-app-configs-reducer';
import authorizationReducer from './authorization-reducer';
import globalConfReducer from './global-conf-reducer';
import networkConfReducer from './network-conf-reducer';
import conf3GReducer from './3g-conf-reducer';
import frequencyConfReducer from './frequency-conf-reducer';
import frequencyPresetsReducer from './frequency-presets-reducer';
import aboutReducer from './about-reducer';
import appSettingsReducer from './app-settings-reducer';

const initStore = {
		language: 'ru',
		langFromDeviceState: 'EMPTY',
		isLogin: false,
		loginToken: null,
		isSupport3G: false,
		expertMode: false,
		aboutState: 'EMPTY',
		aboutError: null,
		isLoadUpdate: false,
		isNeedConfirm: false,
		confirmText: null,
		confirmAction: null,
		deviceActionsError: null,
		about: {
			client: {
				version: '0.1.1'
			},
			server: {},
			os: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		global_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		network_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		conf_3G: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		frequency_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			additionalChannels: {},
			currentAdditionalChannels: {},
			isDataValid: {},
			descriptionOfNotValid: '',
			radios: {},
			currentRadios: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		frequency_presets: {
			selectedItem: 'manual',
			listState: 'EMPTY',
			list: [],
			presetsDataState: {},
			presetsData: {},
			presetsDataErrors: {},
			error: null,
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		appSettings: {
		    managerSettings: {
		        dataState: 'EMPTY',
		        data: {},
		        error: null,
			    isNeedConfirm: false,
			    confirmText: null,
			    confirmAction: null
		    },
			dashboardSettings: {
		        dataState: 'EMPTY',
		        data: {},
		        error: null,
			    isNeedConfirm: false,
			    confirmText: null,
			    confirmAction: null
		    },
		    passwordSettings: {
		        data: {
		            currentPassword: '',
		            newPassword: '',
		            newPassword2: ''
		        },
		        isValid: {
		            currentPassword: false,
		            newPassword: false,
		            newPassword2: true
		        },
		        error: null,
		        isNeedConfirm: false,
		        confirmText: null,
		        confirmAction: null
		    }
		}
	};

const reducer = (store = initStore, action) => {
	switch (action.type) {
		case 'SET_LANGUAGE':
		case 'FETCH_DEVICE_LANGUAGE_LOADING':
		case 'FETCH_DEVICE_LANGUAGE_SUCCESS':
		case 'FETCH_DEVICE_LANGUAGE_FAILURE':
		case 'DROP_ALL_SETTINGS_DATA':
		case 'DEVICE_ACTIONS_ERROR':
		case 'TOGGLE_EXPERT_MODE':
		case 'SHOW_UPDATE_LOADING':
        case 'HIDE_UPDATE_LOADING':
		case 'MAIN_OPEN_CONFIRM':
		case 'MAIN_CLOSE_CONFIRM':{
            return {
				...store,
                ...mainAppConfigsReducer(store, action)
            }
        }

		case 'LOGIN':
		case 'LOGOUT': {
			return {
				...store,
				...authorizationReducer(store, action)
			}
		}

		case 'FETCH_GLOBAL_CONFIGS_REQUEST':
		case 'FETCH_GLOBAL_CONFIGS_SUCCESS':
		case 'FETCH_GLOBAL_CONFIGS_FAILURE':
		case 'GLOBAL_CONFIGS_ERROR':
		case 'EDIT_GLOBAL_CONFIGS':
		case 'SAVED_GLOBAL_CONFIGS':
		case 'GLOBAL_OPEN_CONFIRM':
		case 'GLOBAL_CLOSE_CONFIRM': {
			return {
				...store,
				...globalConfReducer(store, action)
			}
		}

		case 'FETCH_NETWORK_CONFIGS_REQUEST':
		case 'FETCH_NETWORK_CONFIGS_SUCCESS':
		case 'FETCH_NETWORK_CONFIGS_FAILURE':
		case 'NETWORK_CONFIGS_ERROR':
		case 'EDIT_NETWORK_CONFIGS':
		case 'SAVED_NETWORK_CONFIGS':
		case 'NETWORK_OPEN_CONFIRM':
		case 'NETWORK_CLOSE_CONFIRM': {
			const { network_conf } = store;
			return {
				...store,
				network_conf: networkConfReducer(network_conf, action)
			}
		}

		case 'FETCH_3G_CONFIGS_REQUEST':
		case 'FETCH_3G_CONFIGS_SUCCESS':
		case 'FETCH_3G_CONFIGS_FAILURE':
		case '3G_CONFIGS_ERROR':
		case 'EDIT_3G_CONFIGS':
		case 'SAVED_3G_CONFIGS':
		case '3G_OPEN_CONFIRM':
		case '3G_CLOSE_CONFIRM': {
			const { conf_3G } = store;
			return {
				...store,
				conf_3G: conf3GReducer(conf_3G, action)
			}
		}

		case 'CLEAR_FREQUENCY_CONFIGS':
		case 'FETCH_FREQUENCY_CONFIGS_REQUEST':
		case 'FETCH_FREQUENCY_CONFIGS_SUCCESS':
		case 'FETCH_FREQUENCY_CONFIGS_FAILURE':
		case 'FREQUENCY_CONFIGS_ERROR':
		case 'SET_PRESET_TO_FREQUENCY_CONFIG':
		case 'EDIT_FREQUENCY_CONFIGS':
		case 'EDIT_ADDITIONAL_FREQUENCY_CONFIGS':
		case 'FREQUENCY_PRESETS_FILE_LOADED':
		case 'FREQUENCY_OPEN_CONFIRM':
		case 'FREQUENCY_CLOSE_CONFIRM': {
			return {
				...store,
				...frequencyConfReducer(store, action)
			}
		}

		case 'FETCH_FREQUENCY_PRESETS_LIST_REQUEST':
		case 'FETCH_FREQUENCY_PRESETS_LIST_SUCCESS':
		case 'FETCH_FREQUENCY_PRESETS_LIST_FAILURE':
		case 'FETCH_FREQUENCY_PRESET_REQUEST':
		case 'FETCH_FREQUENCY_PRESET_SUCCESS':
		case 'FETCH_FREQUENCY_PRESET_FAILURE':
		case 'FREQUENCY_PRESETS_ERROR':
		case 'FREQUENCY_PRESETS_SELECT_ITEM':
		case 'FREQUENCY_PRESETS_OPEN_CONFIRM':
		case 'FREQUENCY_PRESETS_CLOSE_CONFIRM': {
			return {
				...store,
				frequency_presets: frequencyPresetsReducer(store, action)
			}
		}

		case 'FETCH_ABOUT_DEVICE_REQUEST':
		case 'FETCH_ABOUT_DEVICE_SUCCESS':
		case 'FETCH_ABOUT_DEVICE_FAILURE': {
			return {
				...store,
				...aboutReducer(store, action)
			}
		}

		case 'FETCH_MANAGER_APP_SETTINGS_REQUEST':
		case 'FETCH_MANAGER_APP_SETTINGS_SUCCESS':
		case 'FETCH_MANAGER_APP_SETTINGS_FAILURE':
		case 'MANAGER_APP_SETTINGS_ERROR':
		case 'EDIT_MANAGER_APP_SETTINGS':
		case 'MANAGER_APP_SETTINGS_OPEN_CONFIRM':
		case 'MANAGER_APP_SETTINGS_CLOSE_CONFIRM':
		case 'FETCH_APP_DASHBOARD_SETTINGS_REQUEST':
        case 'FETCH_APP_DASHBOARD_SETTINGS_SUCCESS':
        case 'FETCH_APP_DASHBOARD_SETTINGS_FAILURE':
		case 'FETCH_APP_DASHBOARD_SETTINGS_ERROR':
		case 'EDIT_DASHBOARD_APP_SETTINGS':
		case 'DASHBOARD_APP_SETTINGS_OPEN_CONFIRM':
		case 'DASHBOARD_APP_SETTINGS_CLOSE_CONFIRM':
		case 'PASSWORD_SETTINGS_EDIT':
		case 'APP_PASSWORD_SETTINGS_ERROR':
		case 'APP_PASSWORD_SETTINGS_CLEAN':
		case 'APP_PASSWORD_SETTINGS_OPEN_CONFIRM':
		case 'APP_PASSWORD_SETTINGS_CLOSE_CONFIRM':{
			return {
				...store,
				appSettings: appSettingsReducer(store.appSettings, action)
			}
		}

		default: return store;
	}
}

export default reducer;
