import React from 'react';

import TextLabel from '../../common/text-label';

import './form-3g-conf.css';

const Form3GConf = ({
	isExpertMode,
	data={},
	currentData={},
	isDataValid={},
	onChangeLabel,
	active=true,
	localization,
	onSubmit
}) => {
	const { $3G } = localization;
	const {
		Init2 = '',
		Phone = '',
		Username = '',
		Password = ''
	} = data;

	const {
		Init2: currentInit2 = '',
		Phone: currentPhone = '',
		Username: currentUsername = '',
		Password: currentPassword = ''
	} = currentData;

	const {
		Init2: isInit2Valid = false,
		Phone: isPhoneValid = false,
		Username: isUsernameValid = false,
		Password: isPasswordValid = false
	} = isDataValid;

	let lablesActive = active;

	const expertModeFields = isExpertMode ? <ExpertModeFields
		data={data}
		currentData={currentData}
		isDataValid={isDataValid}
		onChangeLabel={onChangeLabel}
		active={lablesActive}
		localization={localization}
		onSubmit={onSubmit}
	/> : null;

	return (
		<div className="network-conf-form container-fluid">
            <TextLabel
                title={$3G.Init2.title}
				dataDescription={{
					description: $3G.Init2.description,
					requires: $3G.Init2.validRequires
				}}
                fieldName="Init2"
                fieldValue={Init2}
				currentFieldValue={currentInit2}
                isValid={isInit2Valid}
                notValidDescription={$3G.Init2.validRequires}
                onChangeLabel={onChangeLabel}
                active={lablesActive}
				onSubmit={onSubmit}
            />

			<TextLabel
				title={$3G.Phone.title}
				dataDescription={{
					description: $3G.Phone.description,
					requires: $3G.Phone.validRequires
				}}
				fieldName="Phone"
				fieldValue={Phone}
				currentFieldValue={currentPhone}
				isValid={isPhoneValid}
				notValidDescription={$3G.Phone.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={$3G.Username.title}
				dataDescription={{
					description: $3G.Username.description,
					requires: $3G.Username.validRequires
				}}
				fieldName="Username"
				fieldValue={Username}
				currentFieldValue={currentUsername}
				isValid={isUsernameValid}
				notValidDescription={$3G.Username.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={$3G.Password.title}
				dataDescription={{
					description: $3G.Password.description,
					requires: $3G.Password.validRequires
				}}
				fieldName="Password"
				fieldValue={Password}
				currentFieldValue={currentPassword}
				isValid={isPasswordValid}
				notValidDescription={$3G.Password.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			{expertModeFields}

		</div>
	)
}

const ExpertModeFields = ({
	data={},
	currentData={},
	isDataValid={},
	onChangeLabel,
	active=true,
	localization,
	onSubmit
}) => {
	const { $3G } = localization;
	const {
		REF_IP1 = '',
		REF_IP2 = ''
	} = data;

	const {
		REF_IP1: currentREF_IP1 = '',
		REF_IP2: currentREF_IP2 = ''
	} = currentData;

	const {
		REF_IP1: isREF_IP1Valid = false,
		REF_IP2: isREF_IP2Valid = false
	} = isDataValid;

	return (
		<React.Fragment>
			<TextLabel
				title={$3G.REF_IP1.title}
				dataDescription={{
					description: $3G.REF_IP1.description,
					requires: $3G.REF_IP1.validRequires
				}}
				fieldName="REF_IP1"
				fieldValue={REF_IP1}
				currentFieldValue={currentREF_IP1}
				isValid={isREF_IP1Valid}
				notValidDescription={$3G.REF_IP1.validRequires}
				onChangeLabel={onChangeLabel}
				active={active}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={$3G.REF_IP2.title}
				dataDescription={{
					description: $3G.REF_IP2.description,
					requires: $3G.REF_IP2.validRequires
				}}
				fieldName="REF_IP2"
				fieldValue={REF_IP2}
				currentFieldValue={currentREF_IP2}
				isValid={isREF_IP2Valid}
				notValidDescription={$3G.REF_IP2.validRequires}
				onChangeLabel={onChangeLabel}
				active={active}
				onSubmit={onSubmit}
			/>
		</React.Fragment>
	)
}

const Form3GConfButtons = ({ dataState, setConfigs, setDefault, onReRequestData, localization }) => {
	const { $3G } = localization;
	const setConfigsButton = dataState === 'READY' ? <li><button className="btn btn-success" onClick = {setConfigs}>{$3G.buttons.save}</button></li> : null;
	return (
		<div className = "bottom-buttons col-xs-12">
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
						{setConfigsButton}
						<li><button className="btn btn-default" onClick = {onReRequestData}>{$3G.buttons.reRequest}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
	)
}

export { Form3GConf, Form3GConfButtons };
