import checkDataDifferent from '../utils/check-data-different';
import { splitFrequencyData } from '../utils/split-frequency-data';
import channelsInFrequency from '../utils/channels-in-frequency';

// TODO: move validation to actions
import { frequencyValidator } from '../utils/page-data-validators';

const initialFrequencyConfStore = {
	dataState: 'EMPTY',
	error: null,
	currentData: {},
	data: {},
	additionalChannels: {},
	currentAdditionalChannels: {},
	isDataValid: {},
	descriptionOfNotValid: '',
	radios: {},
	currentRadios: {},
	isChanged: false,
	isNeedConfirm: false,
	confirmText: null,
	confirmAction: null
}

const frequencyConfReducer = (store, action) => {
	const { frequency_conf: frequencyConfStore = initialFrequencyConfStore } = store;

    switch (action.type) {
		case 'CLEAR_FREQUENCY_CONFIGS': {
			return {
				frequency_conf: initialFrequencyConfStore
			};
		}

		case 'FETCH_FREQUENCY_CONFIGS_REQUEST': {
			return {
				frequency_conf: {
					...initialFrequencyConfStore,
					dataState: 'LOADING',
				}
			}
		}

		case 'FETCH_FREQUENCY_CONFIGS_SUCCESS': {
			const { payload } = action;
			const { presetsData = {}, presetsDataState = {} } = store.frequency_presets;

			let newSelectedItem = 'manual';
			for (let key in presetsData) {
				if (key !== 'manual') {
					const unitedDataObject = {
						currentData: {...payload.data, ...payload.radios},
						data: presetsData[key]
					}
					if (presetsDataState[key] === 'READY') {
						if (!checkDataDifferent(unitedDataObject)) {
							newSelectedItem = key;
							break;
						}
					}
				}
			}

			// TODO: make deep copy for 'currentData' and 'currentRadios'

			return {
				frequency_conf: {
					...initialFrequencyConfStore,
					dataState: 'READY',
					currentData: payload.data,
					currentAdditionalChannels: payload.additionalChannels,
					currentRadios: payload.radios,
					data: payload.data,
					additionalChannels: payload.additionalChannels,
					isDataValid: payload.isDataValid,
					descriptionOfNotValid: payload.descriptionOfNotValid,
					radios: payload.radios
				},
				frequency_presets: {
					...store.frequency_presets,
					selectedItem: newSelectedItem,
					currentDevicePreset: newSelectedItem,
					presetsData: {
						...presetsData,
						manual: {
							...payload.data,
							...payload.radios
						}
					}
				}
			}
		}

		case 'FETCH_FREQUENCY_CONFIGS_FAILURE': {
			const { payload } = action;
			return {
				frequency_conf: {
					...frequencyConfStore,
					dataState: 'ERROR',
					error: payload
				}
			}
		}

		case 'FREQUENCY_CONFIGS_ERROR': {
			const { payload } = action;
			return {
				frequency_conf: {
					...frequencyConfStore,
					error: payload
				}
			}
		}

		// TODO: move validating to actions
		case 'SET_PRESET_TO_FREQUENCY_CONFIG': {
			const { payload } = action;
			const { presetsData } = store.frequency_presets;
			const preset = presetsData[payload];
			if (typeof preset === 'object') {

				const {
					data,
					radios
				} = splitFrequencyData(preset)

				const {
					descriptionOfNotValid,
					isDataValid
				} = frequencyValidator(data, radios);

				const result = {
					...frequencyConfStore,
					data,
					isDataValid,
					descriptionOfNotValid,
					radios
				}

				const isChanged = checkDataDifferent(result);
				return {
					frequency_conf: {
						...result,
						isChanged
					},
					frequency_presets: {
						...store.frequency_presets,
						selectedItem: payload
					}
				}
			}
			return {

			}
		}

		case 'EDIT_FREQUENCY_CONFIGS': {
			const { payload } = action;
			const { presetsData = {}, presetsDataState = {} } = store.frequency_presets;

			const payloadConfigs = channelsInFrequency(payload);
			let newSelectedItem = 'manual';
			for (let key in presetsDataState) {
				const presetChannels = channelsInFrequency(splitFrequencyData(presetsData[key]));
				const unitedDataObject = {
					currentData: payloadConfigs,
					data: presetChannels
				}
				if (presetsDataState[key] === 'READY') {
					if (!checkDataDifferent(unitedDataObject)) {
						newSelectedItem = key;
						break;
					}
				}
			}

			const result = {
				...frequencyConfStore,
				data: payload.data,
				isDataValid: payload.isDataValid,
				descriptionOfNotValid: payload.descriptionOfNotValid,
				radios: payload.radios
			}
			const isChanged = checkDataDifferent(result);

			return {
				frequency_conf: {
					...result,
					isChanged
				},
				frequency_presets: {
					...store.frequency_presets,
					selectedItem: newSelectedItem,
					presetsData: {
						...presetsData,
						manual: {
							...payload.data,
							...payload.radios
						}
					}
				}
			}
		}

		case 'EDIT_ADDITIONAL_FREQUENCY_CONFIGS': {
			const { payload } = action;
			const {
				channelName,
				field,
				value
			} = payload;
			const result = {
				...frequencyConfStore,
				additionalChannels: {
					...frequencyConfStore.additionalChannels,
					[channelName]: {
						...frequencyConfStore.additionalChannels[channelName],
						[field]: value
					}
				}
			}

			return {
				frequency_conf: {
					...result
				}
			}
		}

		// TODO: make file loaded
		case 'FREQUENCY_PRESETS_FILE_LOADED': {
			const { payload } = action;
			const { presetsData = {}, presetsDataState = {} } = store.frequency_presets;

			const payloadConfigs = channelsInFrequency(payload);
			let newSelectedItem = 'fromFile';
			// TODO: change 'presetsData' in 'for' to 'presetsDataState' and delete 'if'
			for (let key in presetsData) {
				if (key !== 'manual') {
					const presetChannels = channelsInFrequency(splitFrequencyData(presetsData[key]));
					const unitedDataObject = {
						currentData: payloadConfigs,
						data: presetChannels
					}
					if (presetsDataState[key] === 'READY') {
						if (!checkDataDifferent(unitedDataObject)) {
							newSelectedItem = key;
							break;
						}
					}
				}
			}

			const result = {
				...frequencyConfStore,
				data: payload.data,
				isDataValid: payload.isDataValid,
				descriptionOfNotValid: payload.descriptionOfNotValid,
				radios: payload.radios
			}
			const isChanged = checkDataDifferent(result);

			return {
				frequency_conf: {
					...result,
					isChanged
				},
				frequency_presets: {
					...store.frequency_presets,
					selectedItem: newSelectedItem,
				}
			}
		}

		case 'FREQUENCY_OPEN_CONFIRM': {
			const { payload } = action;
			const { confirmText, confirmAction } = payload;
			return {
				frequency_conf: {
					...frequencyConfStore,
					isNeedConfirm: true,
					confirmText,
					confirmAction
				}
			}
		}

		case 'FREQUENCY_CLOSE_CONFIRM': {
			return {
				frequency_conf: {
					...frequencyConfStore,
					isNeedConfirm: false,
					confirmText: null,
					confirmAction: null
				}
			}
		}
        default: return {};
    }
}

export default frequencyConfReducer;
