import { numberValidator } from '../utils/type-data-validators';
import { frequencyValidator, isObjectValid, addChanFreqValidator  } from '../utils/page-data-validators';
//import { putPresetInStore } from './frequency-configs-preset-items-actions';
import { splitFrequencyData } from '../utils/split-frequency-data';

const clearFrequencyConfigs = () => {
	return {
		type: 'CLEAR_FREQUENCY_CONFIGS'
	}
}

const frequencyConfigsRequested = () => {
	return {
		type: 'FETCH_FREQUENCY_CONFIGS_REQUEST'
	}
}

const frequencyConfigsLoaded = (incomingData, dispatch) => {
	const {SX1301_conf} = incomingData;

	const splitedData = splitFrequencyData(SX1301_conf);
	const {
		descriptionOfNotValid,
		isDataValid
	} = frequencyValidator(splitedData.data, splitedData.radios);

	const payload = {
		...splitedData,
		isDataValid,
		descriptionOfNotValid,
	}
	return {
		type: 'FETCH_FREQUENCY_CONFIGS_SUCCESS',
		payload
	}
}

const frequencyConfigsFailure = (payload) => {
	return {
		type: 'FETCH_FREQUENCY_CONFIGS_FAILURE',
		payload
	}
}

const frequencyConfigsError = (payload) => {
	return {
		type: 'FREQUENCY_CONFIGS_ERROR',
		payload
	}
}

const frequencyConfigsEdit = ( field, channelName, value, inFrequencyData, oldRadios ) => {

	// prepare 'newConfigs'
	const preparedVal = field==='if' ? numberValidator(value, 9).data : value;
	const newConfigs = {
		...inFrequencyData,
		[channelName]: {
			...inFrequencyData[channelName],
			[field]: preparedVal
		}
	}

	// separate channels frequencies between radios
	let radiosObj = {
		radio_0: [],
		radio_1: []
	};
	for (let key in newConfigs) {
		if(newConfigs[key].enable) {
			radiosObj['radio_'+newConfigs[key].radio] = [
				...radiosObj['radio_'+newConfigs[key].radio],
				newConfigs[key].if
			]
		}
	}

	// calculate result radios
	let resultRadios = {
		radio_0: {},
		radio_1: {}
	}

	for (let key in radiosObj) {
		resultRadios[key]['freq'] = oldRadios[key].freq;
		if (radiosObj[key].length > 0) {

			let minFreq = radiosObj[key][0];
			let maxFreq = radiosObj[key][0];
			for (var i in radiosObj[key]) {
				minFreq = radiosObj[key][i] < minFreq ? radiosObj[key][i] : minFreq;
				maxFreq = radiosObj[key][i] > maxFreq ? radiosObj[key][i] : maxFreq;
			}

			resultRadios[key]['freq'] = Math.floor((minFreq + maxFreq) / 2) ;
		}
	}

	// calculate result 'if' and create general 'data'
	let resultData = {...newConfigs};
	for (let key in newConfigs) {
		const radioFreq = resultRadios['radio_'+newConfigs[key].radio].freq;
		resultData = {
			...resultData,
			[key]: {
				...newConfigs[key],
				if: newConfigs[key].if - radioFreq
			}
		}
	}
	//console.log(resultData);
	//console.log(frequencyValidator(resultData, resultRadios))

	const {
		descriptionOfNotValid,
		isDataValid
	} = frequencyValidator(resultData, resultRadios);

	return {
		type: 'EDIT_FREQUENCY_CONFIGS',
		payload: {
			data: resultData,
			radios: resultRadios,
			isDataValid,
			descriptionOfNotValid
		}
	}
}

const additionalFrequencyConfigsEdit = ( field, channelName, value, radioFreq ) => {

	const resultValue = addChanFreqValidator(field, value).data;



	return {
		type: 'EDIT_ADDITIONAL_FREQUENCY_CONFIGS',
		payload: {
			channelName: channelName,
			field: field,
			value: (field === 'if' ? resultValue - radioFreq : resultValue)
		}
	}
}

const frequencyOpenConfirm = (payload) => {
	return {
		type: 'FREQUENCY_OPEN_CONFIRM',
        payload
	}
}

const frequencyCloseConfirm = () => {
	return {
		type: 'FREQUENCY_CLOSE_CONFIRM'
	}
}

const fetchFrequencyConfigs = (loginToken, ApiService) => (dispatch) => {
	    const head = {
        cmd: 'get_freq_conf',
        loginToken
    };
    dispatch(frequencyConfigsRequested());
    ApiService.frequencyConfigsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(frequencyConfigsLoaded(response.data, dispatch))
			} else {
				dispatch(frequencyConfigsFailure(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(frequencyConfigsFailure('cant_connect_to_server'));
        });
}

const sendFrequencyConfigs = (data, radios, additionalChannels, isDataValid, loginToken, ApiService) => (dispatch) => {
	const body = {
		...data,
		...radios,
		...additionalChannels
	}

	const head = {
		cmd: 'set_freq_conf',
		loginToken
	};
	if(isObjectValid(isDataValid)) {
		dispatch(frequencyCloseConfirm());

		ApiService.frequencyConfigsRequest(head, body)
			.then((response) => {
				if (response.result) {
					fetchFrequencyConfigs(loginToken, ApiService)(dispatch);
				} else {
					dispatch(frequencyConfigsError(response.msg));
				}
			})
			.catch((er) => {
				console.log("ERROR - sendConfigs", er);
				dispatch(frequencyConfigsError('cant_connect_to_server'));
			});
	} else {
		console.log('isDataValid = false');
	}
}

export {
	clearFrequencyConfigs,
    fetchFrequencyConfigs,
	frequencyConfigsEdit,
	additionalFrequencyConfigsEdit,
	frequencyOpenConfirm,
	frequencyCloseConfirm,
	sendFrequencyConfigs
}
