import React from 'react';

import { withLocalization } from '../../../hoc';

import './authorization-form.css';

const AuthorizationForm = ({ login, password, changeLabel, warning, sendData, localization }) => {

    const {authorizationForm = {}} = localization;
    const authorizationFormWarning = (warning === undefined || warning === null) ? null : <AuthorizationFormWarning warning={authorizationForm[warning]}/>
    const onEnter = (event) => {
        if(event.key === 'Enter') {
            sendData();
        };
    }
    return (
        <div className="authorization-form ">
            <div>
                <div className="col-xs-6">
					{authorizationForm.login}
				</div>
				<div className="col-xs-6">
					<input
                        type="text"
                        name="login"
                        value={login}
                        onChange={(item) => changeLabel('login', item.target.value)}
                        onKeyPress={onEnter}
                    />
                </div>
                <div className="col-xs-6">
					{authorizationForm.password}
                </div>
                <div className="col-xs-6">
					<input
                        type="password"
                        name="password"
                        value={password}
                        onChange={(item) => changeLabel('password', item.target.value)}
                        onKeyPress={onEnter}
                    />
				</div>
            </div>
            {authorizationFormWarning}
            <button className="btn btn-default" onClick={sendData}>{authorizationForm.loginButton}</button>
        </div>
    )
}

const AuthorizationFormWarning = ({ warning }) => {
    return (
        <div className="col-xs-12 caution">
            {warning}
        </div>
    )
}

export default withLocalization(AuthorizationForm);
