const initialAboutStore = {
    aboutState: 'EMPTY',
    about: {
        client: {
            version: '0.1.1'
        },
        server: {},
        os: {},
    },
    aboutError: null
}

const aboutReducer = (store = initialAboutStore, action) => {
    const { type, payload } = action;
    switch (type) {
        case 'FETCH_ABOUT_DEVICE_REQUEST': {
            return {
                about: initialAboutStore.about,
                aboutState: 'LOADING'
            }
        }
		case 'FETCH_ABOUT_DEVICE_SUCCESS': {
            return {
                about: {
                    ...initialAboutStore.about,
                    ...payload
                },
                aboutState: 'READY'
            }
        }
		case 'FETCH_ABOUT_DEVICE_FAILURE': {
            return {
                about: initialAboutStore.about,
                aboutState: 'ERROR',
                aboutError: payload
            }
        }
        default: return {}
    }
}

export default aboutReducer;
