import {
    portValidator,
    ipValidator,
    netMaskValidator,
    eth0Validator,
    domainAdressValidator,
    providerPhoneValidator,
    namePassValidator,
    numberValidator
} from './type-data-validators'

/*
*   check validation for send to server
*
*/
const isObjectValid = (obj) => {
    let result = true;
    const deepObj = (object) => {
        for(var prop in object) {
            if (typeof object[prop] === 'object') {
                deepObj(object[prop]);
            } else {
                if (!object[prop]) {
                    result = false;
                    break;
                }
            }
        }
        return result;
    }
     return deepObj(obj);
}

/*
*   create object of pair objects data and isValid
*   each of this objects has field name and corrected resultValue
*   this object intended for reducer
*   @param String field
*   @param  value
*
*   @return {
        data: {field: data},
        isValid: {field: bool}
    }
*/
const globalConfValidator = (field, value) => {
    let resultValue = null;
    switch (field) {
        case 'serv_port_up':
        case 'serv_port_down': {
            resultValue = portValidator(value);
            break;
        }
        case 'server_address': {
            let isValid = false;
            const cutValue = value.substr(0, 32);
            if (ipValidator(cutValue).data === cutValue && ipValidator(cutValue).isValid) {
                isValid = true;
            } else if (domainAdressValidator(cutValue).isValid) {
                isValid = true;
            } else if (cutValue === 'localhost') {
                isValid = true;
            }
            resultValue = {data: cutValue, isValid}
            break;
        }
        default: {
            resultValue = {data: value, isValid: false}
        }
    }

    return {
        data: {[field]: resultValue.data},
        isValid: {[field]: resultValue.isValid}
    }
}

const networkConfValidator = (field, value) => {
    let resultValue = null;
    switch (field) {
        case 'address':
        case 'gateway':
        case 'nameserver0':
        case 'nameserver1': {
            resultValue = ipValidator(value);
            break;
        }
        case 'netmask': {
            resultValue = ipValidator(value);
            resultValue.isValid = netMaskValidator(resultValue.data);
            break;
        }
        case 'eth0': {
            resultValue = eth0Validator(value);
            break;
        }
        case 'domain': {
            resultValue = domainAdressValidator(value);
            break;
        }
        default: {
            // TODO: make validator mechanism for arrays
            //resultValue = ipValidator(field);

            console.log(field, value);
            resultValue = {data: value, isValid: false}
        }
    }

    return {
        data: {[field]: resultValue.data},
        isValid: {[field]: resultValue.isValid}
    }
}


const conf3GValidator = (field, value) => {
    let resultValue = null;
    switch (field) {
        case 'Init2': {
            resultValue = domainAdressValidator(value);
            break;
        }
        case 'Phone': {
            resultValue = providerPhoneValidator(value);
            break;
        }
        case 'Password':
        case 'Username': {
            resultValue = namePassValidator(value);
            break;
        }
        case 'REF_IP1':{
            resultValue = ipValidator(value);
            break;
        }
        case 'REF_IP2': {
            resultValue = ipValidator(value);
            if (resultValue.data === '') {
                resultValue = {data: '', isValid: true}
            }
            break;
        }
        default: {
            resultValue = {data: value, isValid: false}
        }
    }
    return {
        data: {[field]: resultValue.data},
        isValid: {[field]: resultValue.isValid}
    }
}


/**
 *
 * @return {object} of:
 * isDataValid {object} of validation result of radios
 * descriptionOfNotValid {function(dictionary)} the function returns object of channels with validation descriptions
 */
const frequencyValidator = (data, radios) => {
    /**
     *  Make description text of valid for one channel
     *  @param: {string} validation type
     *  @param: {object} of strings of names, values, etc for insert into description.
     *
     *  @return: {function}
     *  @param: {object} localization dictionary.
     *  @return: {string} description of valid
     */
    const validateTextResultRow = (validationType, data) => (dictionary) => {
        switch (validationType) {
            case 'outOfRange': {
                const { outOfRange = '' } = dictionary.warnings;
                const radioName = dictionary.configs[data.radio] || data.radio;
                let newText = outOfRange;
                newText = newText.split("<{devValue}>").join(data.devValue);
                newText = newText.split("<{radio}>").join(radioName);
                newText = newText.split("<{radioFreq}>").join(data.radioFreq);
                return newText
            }
            case 'smallInterval': {
                const { smallInterval = '' } = dictionary.warnings;
                const firstChannelName = dictionary.configs[data.firstChannelName] || data.firstChannelName;
                const secondChannelName = dictionary.configs[data.secondChannelName] || data.secondChannelName;
                let newText = smallInterval;
                newText = newText.split("<{firstChannelName}>").join(firstChannelName);
                newText = newText.split("<{secondChannelName}>").join(secondChannelName);
                newText = newText.split("<{interval}>").join(data.interval);
                return newText
            }
            case 'radioFreqNotValid': {
                const { radioFreqOutOfRange = '' } = dictionary.warnings;
                const radioName = dictionary.configs[data.radio] || data.radio;
                let newText = radioFreqOutOfRange;
                newText = newText.split("<{radio}>").join(radioName);
                newText = newText.split("<{radioFreq}>").join(data.radioFreq);
                return newText
            }
            default: {
                return null
            }
        }
    };

    /**
     *  Merge objects of functions of which returns description of not valid data.
     *  @param {objects of functions}
     *  @param {objects of functions}
     *  @param {objects of functions}
     *  @param {objects} channels
     *
     *  @return {function}
     *  @param {object} dictionary
     *  @return {object of string} description of valid for channels
     */
    const returnValid = (rangeValid, intervalValid, radioFreqValidDescription, channels) => (dictionary) => {
        let result = {};
        for (let channel in channels) {
            if (typeof radioFreqValidDescription[channel] === 'function') {
                result = {
                    ...result,
                    [channel]: radioFreqValidDescription[channel](dictionary)
                }
            };
            if (typeof rangeValid[channel] === 'function') {
                result = {
                    ...result,
                    [channel]: rangeValid[channel](dictionary)
                }
            };
            if (typeof intervalValid[channel] === 'function') {
                result = {
                    ...result,
                    [channel]: intervalValid[channel](dictionary)
                }
            };
        };
        return result;
    };

    let rangeValidDescription = {};
    let radioFreqValidDescription = {};
    let validationResult = {};
    let channels = [];
    for (let key in radios) {
        validationResult[key] = true;
    }

    /*
    *   Check elements to includes in the range of radio.
    *   Fill channels array
    */
    for (let key in data) {
        if (data[key].enable) {
            if (data[key].if < -400000 || data[key].if > 400000) {

                rangeValidDescription[key] = validateTextResultRow(
                    'outOfRange',
                    {
                        devValue: data[key].if,
                        radio: 'radio_'+data[key].radio,
                        radioFreq: radios['radio_'+data[key].radio].freq
                    }
                )

                validationResult['radio_'+data[key].radio] = false;
            };
            /**
             *  Check radios frequency valid
             */
            if (radios['radio_'+data[key].radio].freq < 863000000 || radios['radio_'+data[key].radio].freq > 870000000) {
                radioFreqValidDescription[key] = validateTextResultRow(
                    'radioFreqNotValid',
                    {
                        radio: 'radio_'+data[key].radio,
                        radioFreq: radios['radio_'+data[key].radio].freq
                    }
                )
                validationResult['radio_'+data[key].radio] = false;
            };

            channels = [
                ...channels,
                {
                    ...data[key],
                    channelName: key,
                    if: data[key].if+radios['radio_'+data[key].radio].freq
                }
            ];
        }
    }

    /*
    *   Check intervals between elements
    */
    let intervalValidDescription = {}
    if (channels.length > 0) {
        channels.sort(( a, b ) =>  a.if - b.if).reduce((prev, current)=>{
            const diff = current.if - prev.if;
            if ((diff) < 200000) {
                validationResult['radio_' + current.radio] = false;
                validationResult['radio_' + prev.radio] = false;

                intervalValidDescription[prev.channelName] = validateTextResultRow(
                    'smallInterval',
                    {
                        firstChannelName: prev.channelName,
                        secondChannelName: current.channelName,
                        interval: diff
                    }
                );
                intervalValidDescription[current.channelName] = validateTextResultRow(
                    'smallInterval',
                    {
                        firstChannelName: current.channelName,
                        secondChannelName: prev.channelName,
                        interval: diff
                    }
                );
            } else {
            }
            return current;
        })
    }

    return {
        isDataValid: validationResult,
        descriptionOfNotValid: returnValid(rangeValidDescription, intervalValidDescription, radioFreqValidDescription, data) //return function(dictionary)
    };
};

const addChanFreqValidator = (field, value) => {
    switch (field) {
        case 'if': {
            return numberValidator(value, 9);
        }
        case 'radio': {
            return {
                data: value === 1 ? 1 : 0,
                isValid: true
            }
        }
        case 'enable': {
            return {
                data: value || false,
                isValid: true
            }
        }
        case 'bandwidth':
        case 'datarate':
        case 'spread_factor': {
            return numberValidator(value, 9);
        }
        default: {
            return {
                data: value,
                isValid: false
            }
        }
    }
}


export {
    globalConfValidator,
    networkConfValidator,
    conf3GValidator,
    frequencyValidator,
    addChanFreqValidator,

    isObjectValid
}
