import React from 'react';

import './localization-toggle.css';

const LocalizationToggle = ({ selected, onSelect }) => {
    return (
        <div className="localization-toggle pull-right">
            <button className={selected==='ru' ? "checked" : null} onClick={() => onSelect('ru')}>RU</button>
            <button className={selected==='en' ? "checked" : null} onClick={() => onSelect('en')}>EN</button>
        </div>
    )
}

export default LocalizationToggle;
