
/**
*   putPresetInStore
*
*   @param {object}
    {
        presetData: { //object
            radio_0,
            readio_1,
            chan_multiSF_0,
            ...
        },
        presetName //string
    }
*/
const putPresetInStore = (payload) => {
    return {
        type: 'FETCH_FREQUENCY_PRESET_SUCCESS',
        payload
    }
}

const frequencyPresetRequested = (payload) => {
    return {
        type: 'FETCH_FREQUENCY_PRESET_REQUEST',
        payload
    }
}

const frequencyPresetFailure = (payload) => {
    return {
        type: 'FETCH_FREQUENCY_PRESET_FAILURE',
        payload
    }
}

const onSelectItem = (payload) => {
    return {
        type: 'SET_PRESET_TO_FREQUENCY_CONFIG',
        payload
    }
}

const fetchPresetFrequencyConfigs = (loginToken, ApiService, presetName) => (dispatch) => {
    const head = {
        cmd: 'get_freq_preset',
        loginToken
    };
    const body = {
        presetName
    }
    dispatch(frequencyPresetRequested({ presetName }));
    ApiService.frequencyConfigsRequest(head, body)
        .then((response) => {
			if (response.result) {
				dispatch(putPresetInStore({ presetName, presetData: response.data }))
			} else {
				dispatch(frequencyPresetFailure({ presetName, errorText: presetName + ' server ERROR: ' + response.msg }));
                console.error(presetName, 'server ERROR:', response.msg);
                dispatch(onSelectItem('manual'));
			}
		})
        .catch((er) => {
            dispatch(frequencyPresetFailure({ presetName, errorText: 'cant_connect_to_server'}));
            console.error(presetName, 'server ERROR:', er);
            dispatch(onSelectItem('manual'));
        });
}

export {
    putPresetInStore, //???
    fetchPresetFrequencyConfigs,
    onSelectItem,
}
