import { deepMap } from '../utils/objects-utils/deep-map-obj';
import { isObjectValid } from '../utils/page-data-validators';
import {
    appManagerSettingsValidator,
    appPasswordSettingsValidator,
    dashboardAppSettingsValidator
} from '../utils/validators/app-settings-validators';

/**
 * Manager settings acrions
 */
const fetchManagerAppSettingsRequest = () => {
    return {
        type: 'FETCH_MANAGER_APP_SETTINGS_REQUEST'
    }
}

const fetchManagerAppSettingsSuccess = (data) => {
    const isValid = deepMap(data, (key, val) => {
		const result = appManagerSettingsValidator(key, val).isValid;
		return (result);
	});

    return {
        type: 'FETCH_MANAGER_APP_SETTINGS_SUCCESS',
        payload: {data, isValid}
    }
}

const fetchManagerAppSettingsFailure = (payload) => {
    return {
        type: 'FETCH_MANAGER_APP_SETTINGS_FAILURE',
        payload
    }
}

const managerAppSettingsError = (payload) => {
    return {
        type: 'MANAGER_APP_SETTINGS_ERROR',
        payload
    }
}

const editManagerAppSettings = (payload) => {
    const resultValid = appManagerSettingsValidator(payload.field, payload.val);
    return {
        type: 'EDIT_MANAGER_APP_SETTINGS',
        payload: {...payload, isValid: resultValid.isValid, val: resultValid.data}
    }
}

const managerAppSettingsOpenConfirm = (payload) => {
	return {
		type: 'MANAGER_APP_SETTINGS_OPEN_CONFIRM',
        payload
	}
}

const managerAppSettingsCloseConfirm = () => {
	return {
		type: 'MANAGER_APP_SETTINGS_CLOSE_CONFIRM'
	}
}


const fetchManagerAppSettings = ({loginToken, ApiService}) => (dispatch) => {
    const head = {
        cmd: 'get_manager_settings',
        loginToken
    }

    dispatch(fetchManagerAppSettingsRequest());

    ApiService.appSettingsRequest(head)
    .then((response) => {
        if (response.result) {
            dispatch(fetchManagerAppSettingsSuccess(response.data))
        } else {
            dispatch(fetchManagerAppSettingsFailure(response.msg));
        }
    })
    .catch((er) => {
        console.error("ERROR", er);
        dispatch(fetchManagerAppSettingsFailure('cant_connect_to_server'));
    });
};


const sendManagerAppSettings = ({loginToken, ApiService, data}) => (dispatch) => {
    const head = {
        cmd: 'set_manager_settings',
        loginToken
    }

    dispatch(managerAppSettingsCloseConfirm());

    ApiService.appSettingsRequest(head, data)
    .then((response) => {
        if (response.result) {
            fetchManagerAppSettings({loginToken, ApiService})(dispatch);
        } else {
            dispatch(managerAppSettingsError(response.msg));
        }
    })
    .catch((er) => {
        console.error("ERROR", er);
        dispatch(managerAppSettingsError('cant_connect_to_server'));
    });
};

/**
 * Password settings actions
 */
const passwordSettingsEdit = (field, val, isPasswordsSame) => {
    const result = appPasswordSettingsValidator(field, val, isPasswordsSame)
    const payload = {
        field,
        result
    }
    return {
        type: 'PASSWORD_SETTINGS_EDIT',
        payload
    }
}

const appPasswordSettingsError = (payload) => {
	return {
		type: 'APP_PASSWORD_SETTINGS_ERROR',
        payload
	}
}

const appPasswordSettingsClean = () => {
	return {
		type: 'APP_PASSWORD_SETTINGS_CLEAN'
	}
}

const appPasswordSettingsOpenConfirm = (payload) => {
	return {
		type: 'APP_PASSWORD_SETTINGS_OPEN_CONFIRM',
        payload
	}
}

const appPasswordSettingsCloseConfirm = () => {
	return {
		type: 'APP_PASSWORD_SETTINGS_CLOSE_CONFIRM'
	}
}

const sendAppPasswordSettings = ({loginToken, ApiService, data}) => (dispatch) => {
    const head = {
        cmd: 'set_password_settings',
        loginToken
    }

    dispatch(appPasswordSettingsCloseConfirm());
    dispatch(appPasswordSettingsClean());

    ApiService.appSettingsRequest(head, data)
    .then((response) => {
        if (response.result) {
            alert('#Password was changed');
        } else {
            dispatch(appPasswordSettingsError(response.msg));
        }
    })
    .catch((er) => {
        console.error("ERROR", er);
        dispatch(appPasswordSettingsError('cant_connect_to_server'));
    });
};

/**
 * Dashboard settings actions
 */
const fetchDashboardAppSettingsRequest = () => {
    return {
        type: 'FETCH_APP_DASHBOARD_SETTINGS_REQUEST'
    }
}

const fetchDashboardAppSettingsSuccess = (data) => {
    const isValid = deepMap(data, (key, val, path) => {
		const result = dashboardAppSettingsValidator(path, val).isValid;
		return (result);
	});

    return {
        type: 'FETCH_APP_DASHBOARD_SETTINGS_SUCCESS',
        payload: {data, isValid}
    }
}

const fetchDashboardAppSettingsFailure = (payload) => {
    return {
        type: 'FETCH_APP_DASHBOARD_SETTINGS_FAILURE',
        payload
    }
}

const fetchDashboardAppSettingsError = (payload) => {
    return {
        type: 'FETCH_APP_DASHBOARD_SETTINGS_ERROR',
        payload
    }
}

const dashboardAppSettingsOpenConfirm = (payload, isValid={a: true}) => {
    if (isObjectValid(isValid)) {
    	return {
    		type: 'DASHBOARD_APP_SETTINGS_OPEN_CONFIRM',
            payload
    	}
    } else {
        alert('#There are one or more no valid fields.');
        return {
    		type: 'DASHBOARD_APP_SETTINGS_HAS_NOT_VALID_FIELDS'
    	}
    }
}

const dashboardAppSettingsCloseConfirm = () => {
	return {
		type: 'DASHBOARD_APP_SETTINGS_CLOSE_CONFIRM'
	}
}

const editDashboardAppSettings = ({part, field, val}) => {
    const resultValid = part !== undefined ? dashboardAppSettingsValidator(`Object.${part}.${field}`, val) : dashboardAppSettingsValidator(`Object.${field}`, val);
    return {
        type: 'EDIT_DASHBOARD_APP_SETTINGS',
        payload: {
            part,
            field,
            isValid: resultValid.isValid,
            val: resultValid.data
        }
    }
}

const fetchDashboardAppSettings = ({ loginToken, ApiService }) => (dispatch) => {
    const head = {
        cmd: 'get_dashboard_settings',
        loginToken
    }

    dispatch(fetchDashboardAppSettingsRequest());

    ApiService.appSettingsRequest(head)
    .then((response) => {
        if (response.result) {
            dispatch(fetchDashboardAppSettingsSuccess(response.data))
        } else {
            dispatch(fetchDashboardAppSettingsFailure(response.msg));
        }
    })
    .catch((er) => {
        console.error("ERROR", er);
        dispatch(fetchDashboardAppSettingsFailure('cant_connect_to_server'));
    });
};

const sendDashboardAppSetting = ({ loginToken, ApiService, data }) => (dispatch) => {
    const head = {
        cmd: 'set_dashboard_settings',
        loginToken
    }

    dispatch(dashboardAppSettingsCloseConfirm());

    ApiService.appSettingsRequest(head, data)
    .then((response) => {
        if (response.result) {
            fetchDashboardAppSettings({loginToken, ApiService})(dispatch);
        } else {
            dispatch(fetchDashboardAppSettingsError(response.msg));
        }
    })
    .catch((er) => {
        console.error("ERROR", er);
        dispatch(fetchDashboardAppSettingsError('cant_connect_to_server'));
    });
};

export {
    fetchManagerAppSettings,
    sendManagerAppSettings,
    editManagerAppSettings,
    managerAppSettingsOpenConfirm,
    managerAppSettingsCloseConfirm,

    fetchDashboardAppSettings,
    sendDashboardAppSetting,
    editDashboardAppSettings,
    dashboardAppSettingsOpenConfirm,
    dashboardAppSettingsCloseConfirm,

    passwordSettingsEdit,
    appPasswordSettingsOpenConfirm,
    appPasswordSettingsCloseConfirm,
    sendAppPasswordSettings
}
