import React from 'react';

import './manager-buttons.css';

const AppSettingsManagerButtons = ({ rerequestData, sendData, localization }) => {
    return (
        <div className='bottom-buttons app-settings-manager-buttons'>
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
                        <li><button className="btn btn-success" onClick={sendData}>{localization.buttons.set}</button></li>
                        <li><button className="btn btn-default" onClick={rerequestData}>{localization.buttons.get}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
    )
};

export default AppSettingsManagerButtons;
