import React from 'react';

import { withLocalization } from '../../../hoc';

import './error-indicator.css';

const ErrorIndicator = ({ error, localization }) => {
	const { warnings } = localization;
	return (
		<div className="error-indicator">
			<i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
			<br />
			{warnings[error] === undefined ? error : warnings[error]}
		</div>
	)
}

export default withLocalization(ErrorIndicator);
