import { deepMap } from '../utils/objects-utils/deep-map-obj';
import { globalConfValidator, isObjectValid } from '../utils/page-data-validators';

// TODO: create action
const clearGlobalConfigs = () => {
	return {
		type: 'CLEAR_GLOBAL_CONFIGS'
	}
}

const globalConfigsRequested = () => {
	return {
		type: 'FETCH_GLOBAL_CONFIGS_REQUEST'
	}
}

const globalConfigsLoaded = (data) => {
	const {gateway_ID} = data.gateway_conf;
	let {gateway_conf} = data;
	delete gateway_conf['gateway_ID'];

	const isValid = deepMap(gateway_conf, (key, val) => {
		const result = globalConfValidator(key, val).isValid;
		return (result[key]);
	});
	const payload = {
		data: data.gateway_conf,
		isValid,
		gatewayid: gateway_ID
	}

	return {
		type: 'FETCH_GLOBAL_CONFIGS_SUCCESS',
		payload
	}
}

const globalConfigsFailure = (payload) => {
	return {
		type: 'FETCH_GLOBAL_CONFIGS_FAILURE',
		payload
	}
}

const globalConfigsError = (payload) => {
	return {
		type: 'GLOBAL_CONFIGS_ERROR',
		payload
	}
}

const globalConfigsEdit = (data, currentData, field, value) => {
	const payload = globalConfValidator(field, value);
	return {
		type: 'EDIT_GLOBAL_CONFIGS',
		payload
	}
}

const globalConfigsSaved = () => {
	return {
		type: 'SAVED_GLOBAL_CONFIGS'
	}
}

const globalOpenConfirm = (payload) => {
	return {
		type: 'GLOBAL_OPEN_CONFIRM',
        payload
	}
}

const globalCloseConfirm = () => {
	return {
		type: 'GLOBAL_CLOSE_CONFIRM'
	}
}

const sendGlobalConfigs = (data, isDataValid, loginToken, ApiService) => (dispatch) => {
	const head = {
		cmd: 'set_global_conf',
		loginToken
	};
	if(isObjectValid(isDataValid)) {
		dispatch(globalCloseConfirm());

		ApiService.globalConfigsRequest(head, data)
			.then((response) => {
				if (response.result) {
					fetchGlobalConfigs(loginToken, ApiService)(dispatch);
				} else {
					dispatch(globalConfigsError(response.msg));
				}
			})
			.catch((er) => {
				console.error("ERROR", er);
	            dispatch(globalConfigsError('cant_connect_to_server'));
			});
	} else {
		console.log('isDataValid = false');
	}
}

const fetchGlobalConfigs = (loginToken, ApiService) => (dispatch) => {
	const head = {
        cmd: 'get_global_conf',
        loginToken
    };
    dispatch(globalConfigsRequested());
    ApiService.globalConfigsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(globalConfigsLoaded(response.data))
			} else {
				dispatch(globalConfigsFailure(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(globalConfigsFailure('cant_connect_to_server'));
        });
}

export {
	clearGlobalConfigs,
	globalConfigsError,
	globalConfigsEdit,
	globalConfigsSaved,
    globalOpenConfirm,
    globalCloseConfirm,

    fetchGlobalConfigs,
	sendGlobalConfigs
}
