import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';
import { isObjectValid } from '../../../../utils/page-data-validators';

import {
    passwordSettingsEdit,
    appPasswordSettingsOpenConfirm,
    appPasswordSettingsCloseConfirm,
    sendAppPasswordSettings
} from '../../../../actions';

import ConfirmForm from '../../../views/common/confirm-form';

import AppPasswordSettingsLayout from '../../../views/pages/app-password-settings/layout';
import AppPasswordSettingsForm from '../../../views/pages/app-password-settings/form';
import AppPasswordSettingsButtons from '../../../views/pages/app-password-settings/buttons';

const AppPasswordSettings = ({
    loginToken,
    passwordSettings,

    passwordSettingsEdit,
    appPasswordSettingsOpenConfirm,
    appPasswordSettingsCloseConfirm,
    sendAppPasswordSettings,

    localization,
    ApiService
}) => {
    const { appSettings={} } = localization;
    const { password={} } = appSettings;

    const changeHandler = (field) => (value) => {
        passwordSettingsEdit(field, value);
    };

    const sendData = (confirmerData, isValid) => () => {
        if (isObjectValid(isValid)) {
            appPasswordSettingsOpenConfirm(confirmerData);
        } else {
            alert(localization.confirm.notValidFields);
        }
    }

    const {
        isValid,
        isNeedConfirm,
        data,
        confirmAction,
        confirmText,
        error
    } = passwordSettings;

    const confirmerData = {
        confirmText: password.confirmText,
        confirmAction: () => sendAppPasswordSettings({loginToken, ApiService, data})
    }

    const notValidDescription = {
        currentPassword: isValid.currentPassword ? null : password.currentPassword.notValidCaution,
        newPassword: isValid.newPassword ? null : password.newPassword.notValidCaution,
        newPassword2: isValid.newPassword2 ? null : password.newPassword2.notValidCaution
    }


    let buttons = null;
    if (isNeedConfirm) {
        buttons = <ConfirmForm
            confirmText={confirmText}
            onConfirm={confirmAction}
            onCancel={appPasswordSettingsCloseConfirm}
        />
    } else {
        buttons = <AppPasswordSettingsButtons
            localization={password}
            sendData={sendData(confirmerData, isValid)}
        />
    }

    return (
        <AppPasswordSettingsLayout
            title={password.title}
            appPasswordSettingsForm = <AppPasswordSettingsForm
                localization={password}
                isActive={!isNeedConfirm}
                data={data}
                isValid={isValid}
                notValidDescription={notValidDescription}
                onChange={changeHandler}
                onSubmit={sendData(confirmerData, isValid)}
            />
            appSettingsPasswordButtons={buttons}
            error={error}
        />
    )
}

const mapStoreToProps = ({ loginToken, appSettings }) => {
    const { passwordSettings } = appSettings;
    return {
        loginToken,
        passwordSettings
    }
}

const mapDispatchToProps = {
    passwordSettingsEdit,
    appPasswordSettingsOpenConfirm,
    appPasswordSettingsCloseConfirm,
    sendAppPasswordSettings
}

export default compose(
    withApiService,
    withLocalization,
    connect(mapStoreToProps, mapDispatchToProps)
)(AppPasswordSettings);
