import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';
import {
	fetchGlobalConfigs,
	sendGlobalConfigs,
	globalConfigsEdit,
	globalOpenConfirm, // { confirmText, confirmAction }
	globalCloseConfirm,
} from '../../../../actions';
import { isObjectValid } from '../../../../utils/page-data-validators';

import { withApiService, withLocalization } from '../../../hoc';


import { GlobalConfForm,
	GlobalConfFormButtons
} from '../../../views/pages/global-conf';

import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';
import ConfirmForm from '../../../views/common/confirm-form';
import MainPage from '../../../views/common/main-page';

const GlobalConfPage = ({
    loginToken,
    dataState,
    data,
	currentData,
    isDataValid,
	isNeedConfirm,
	confirmText,
	confirmAction,
    error,

	fetchGlobalConfigs,
	sendGlobalConfigs,
	globalConfigsEdit,
	globalOpenConfirm,
	globalCloseConfirm,

    ApiService,
	localization
}) => {
	const { loraGlobalGateway } = localization;
	const onChangeLabel = (field, value) => {
		if (!isNeedConfirm) {
			globalConfigsEdit(data, currentData, field, value);
		}
	}

	const setConfigs = () => {
		const isValid = isObjectValid(isDataValid);
		if (isValid) {
			globalOpenConfirm(setConfigsConfirmData)
		} else {
            alert(localization.confirm.notValidFields);
        }
	}

	const setConfigsConfirmData = {
		confirmText: loraGlobalGateway.confirmReqests.onSave,
		confirmAction: () => sendGlobalConfigs(data, isDataValid, loginToken, ApiService)
	}
	const reRequestData = {
		confirmText: loraGlobalGateway.confirmReqests.onReRequest,
		confirmAction: () => fetchGlobalConfigs(loginToken, ApiService)
	}

	let content;
	switch (dataState) {
		case 'READY':
			content = <GlobalConfForm
				data={data}
				currentData={currentData}
				isDataValid={isDataValid}
				onChangeLabel={onChangeLabel}
				active={!isNeedConfirm}
				localization={localization}
				onSubmit={setConfigs}
				/>
			break;
		case 'LOADING': content = <Spinner />
			break;
		case 'ERROR': content = <ErrorIndicator error={error}/>
			break;
		default: content = null;
	}

	let buttons = null;
	if (isNeedConfirm) {
		buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>globalCloseConfirm()}/>
	} else {
		buttons = <GlobalConfFormButtons
			dataState={dataState}
			setConfigs={setConfigs}
			onReRequestData={() => globalOpenConfirm(reRequestData)}
			localization={localization}
		/>
	}

	return (
		<MainPage
			name={loraGlobalGateway.title}
			dataState={dataState}
			content={content}
			buttons={buttons}
			error={dataState !== 'ERROR' ? error : null}
		/>
	)
};

const mapStoreToProps = ({ global_conf, loginToken }) => {
	const {
		dataState,
		data,
		currentData,
		isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,
		error
	} = global_conf;
    return {
        loginToken,
        dataState,
        data,
		currentData,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    }
}

const mapDispatchToProps = (dispatch) => {
	return {
		fetchGlobalConfigs: (loginToken, ApiService) => fetchGlobalConfigs(loginToken, ApiService)(dispatch),
		sendGlobalConfigs: (data, isDataValid, loginToken, ApiService) => sendGlobalConfigs(data, isDataValid, loginToken, ApiService)(dispatch),
		globalConfigsEdit: (data, currentData, field, value) => dispatch(globalConfigsEdit(data, currentData, field, value)),
		globalOpenConfirm: (payload) => dispatch(globalOpenConfirm(payload)), // { confirmText, confirmAction }
		globalCloseConfirm: () => dispatch(globalCloseConfirm()),
	}
}

export default compose(
	connect(mapStoreToProps, mapDispatchToProps),
	withApiService,
	withLocalization
)(GlobalConfPage);
