import checkDataDifferent from '../utils/check-data-different';

const initialGlobalConfStore = {
	dataState: 'EMPTY',
	error: null,
	currentData: {},
	data: {},
	isDataValid: {},
	isChanged: false,
	isNeedConfirm: false,
	confirmText: null,
	confirmAction: null
}

const globalConfReducer = (store, action) => {
	const {global_conf: globalConfStore = initialGlobalConfStore} = store;
	switch (action.type) {
		case 'CLEAR_GLOBAL_CONFIGS': {
			return {
			...store,
			global_conf: {
					...initialGlobalConfStore
				}
			}
		}

		case 'FETCH_GLOBAL_CONFIGS_REQUEST': {
			return {
				...store,
				global_conf: {
					...initialGlobalConfStore,
					dataState: 'LOADING',
				},
			}
		}

		case 'FETCH_GLOBAL_CONFIGS_SUCCESS': {
			const { payload } = action;
			return {
				...store,
				global_conf: {
					...initialGlobalConfStore,
					dataState: 'READY',
					currentData: payload.data,
					data: payload.data,
					isDataValid: payload.isValid
				},
				gatewayid: payload.gatewayid
			}
		}

		case 'FETCH_GLOBAL_CONFIGS_FAILURE': {
			const { payload } = action;
			return {
				...store,
				global_conf: {
					...globalConfStore,
					dataState: 'ERROR',
					error: payload
				}
			}
		}

		case 'GLOBAL_CONFIGS_ERROR': {
			const { payload } = action;
			console.log(payload);
			return {
				...store,
				global_conf:{
					...globalConfStore,
					error: payload
				}
			}
		}

		case 'EDIT_GLOBAL_CONFIGS': {
			const { payload } = action;
			const result = {
				...globalConfStore,
				data: {
					...globalConfStore.data,
					...payload.data
				},
				isDataValid: {
					...globalConfStore.isDataValid,
					...payload.isValid
				}

			}
			const isChanged = checkDataDifferent(result);
			return {
				...store,
				global_conf: {
					...result,
					isChanged
				}
			}
		}

		case 'SAVED_GLOBAL_CONFIGS': {
			return {
				...store,
				global_conf: {
					...globalConfStore,
					//isChanged: false
				}
			}
		}

		case 'GLOBAL_OPEN_CONFIRM': {
			const { payload } = action;
			const { confirmText, confirmAction } = payload;
			return {
				...store,
				global_conf: {
					...globalConfStore,
					isNeedConfirm: true,
					confirmText,
					confirmAction
				}
			}
		}

		case 'GLOBAL_CLOSE_CONFIRM': {
			return {
				...store,
				global_conf: {
					...globalConfStore,
					isNeedConfirm: false,
					confirmText: null,
					confirmAction: null
				}
			}
		}
		default: return globalConfStore;
	}
}

export default globalConfReducer;
