const deviceActionsError = (payload) => {
    return {
        type: 'DEVICE_ACTIONS_ERROR',
        payload
    };
};

const showUpdateLoading = () => {
    return {
        type: 'SHOW_UPDATE_LOADING'
    };
};

const hideUpdateLoading = () => {
    return {
        type: 'HIDE_UPDATE_LOADING'
    };
};

const mainOpenConfirm = (payload) => {
	return {
		type: 'MAIN_OPEN_CONFIRM',
        payload
	}
};

const dropAllSettingsData = () => {
    return {
        type: 'DROP_ALL_SETTINGS_DATA'
    };
};

const mainCloseConfirm = () => {
	return {
		type: 'MAIN_CLOSE_CONFIRM'
	}
};

const rebootDeviceAction = (loginToken, ApiService, dictionary) => (dispatch) => {
    const head = {
        cmd: 'reboot_device',
        loginToken
    }
    const { resultNotices } = dictionary;

    dispatch(mainCloseConfirm());

    ApiService.appDeviceActionsRequest(head).then((response) => {
        if (response.result) {
            alert(resultNotices.rebootAccepted);
        } else {
            console.log(response.msg);
            // TODO: Make event handling
            dispatch(deviceActionsError(response.msg));
        }
    }).catch((er) => {
        console.log(er);
        // TODO: Make event handling
        dispatch(deviceActionsError('cant_connect_to_server'));
    })
}

