const updateServerAction = (loginToken, ApiService, dictionary) => (dispatch) => {
    const head = {
        cmd: 'update_server',
        loginToken
    }

    const { resultNotices } = dictionary;

    dispatch(mainCloseConfirm());
    dispatch(showUpdateLoading());

    ApiService.appDeviceActionsRequest(head).then((response) => {
        if (response.result) {
            dispatch(hideUpdateLoading());
            switch (response.msg) {
                case 'done': {
                    alert(resultNotices.updateComplete);
                    break;
                }
                default: {
                    console.log('Update the server - unknown response:' + response.msg);
                }
            }
        } else {
            dispatch(hideUpdateLoading());
            console.log(response.msg);
            dispatch(deviceActionsError(response.msg));
        }
    }).catch((er) => {
        dispatch(hideUpdateLoading());
        console.log(er);
        dispatch(deviceActionsError('cant_connect_to_server'));
    })
}

