import React from 'react';

import PageFrame from '../../../common/page-frame';
import ErrorIndicator from '../../../common/error-indicator';
import DeviceActionsButtons from '../buttons';

import './layout.css';

const DeviceActionsLayout = ({
    localization,
    title,
    deviceActionsButtons=null,
    dashboardActionsButtons=null,
    filesActionsButtons=null,
    confirmer=null,
    error=null
}) => {

    const dashboardActionsSubsection = dashboardActionsButtons !== null ? (
        <div className="subsection row bottom-line">
            <div className="subsection-title">
                {localization.dashboardActionsButtons}
            </div>
            <DeviceActionsButtons
                buttons={dashboardActionsButtons}
            />
        </div>
    ) : null;

    const filesActionsSubsection = filesActionsButtons !== null ? (
        <div className="subsection row bottom-line">
            <div className="subsection-title">
                {localization.filesActionsButtons}
            </div>
            <DeviceActionsButtons
                buttons={filesActionsButtons}
            />
        </div>
    ) : null;

    const deviceActionsSubsection = deviceActionsButtons !== null ? (
        <div className="subsection row">
            <div className="subsection-title">
                {localization.deviceActionsButtons}
            </div>
            <DeviceActionsButtons
                buttons={deviceActionsButtons}
            />
        </div>
    ) : null;

    return (
        <PageFrame name={title}>
            <div className="layout device-actions-layout">
                {error !== null ? <ErrorIndicator error={error} /> : null}
                {filesActionsSubsection}
                {dashboardActionsSubsection}
                {deviceActionsSubsection}
                {confirmer}
            </div>
        </PageFrame>
    )
}

export default DeviceActionsLayout;
