import {
    ipWithPortValidator,
    domainAdressWithoutProtocolValidator,
    partOfUrlPathValidator,
    numberValidator
} from '../type-data-validators'

const appManagerSettingsValidator = (field, value) => {
    switch (field) {
        case 'GIT_NAME':
        case 'GIT_REPO': {
            return partOfUrlPathValidator(value);
        }

        case 'GIT_DOMAIN': {
            let isValid = false;
            if (ipWithPortValidator(value).isValid) {
                isValid = true;
            } else if (domainAdressWithoutProtocolValidator(value).isValid) {
                isValid = true;
            } else if (value === 'localhost') {
                isValid = true;
            }
            return {data: value, isValid}
        }

        case 'GIT_PROVIDER': {
            switch (value) {
                case 'GIT_LAB':
                case 'GIT_HUB': {
                    return {data: value, isValid: true};
                }
                default: {
                    return {data: value, isValid: false};
                }
            }
        }

        case 'TIME_INTERVAL_IN_MINUTES': {
            return numberValidator(value, 4);
        }

        case 'loggerLevel': {
            return {data: value, isValid: true};
        }

        case 'SERVER_STARTUP_METHOD': {
            switch (value) {
                case 'automatically':
                case 'button': {
                    return {data: value, isValid: true};
                }
                default: {
                    return {data: value, isValid: false};
                }
            }
        }

        default: {
            return {data: value, isValid: false};
        }
    }
}

const appPasswordSettingsValidator = (field, value) => {
    switch (field) {
        case 'currentPassword': {
            const resultValue = value.substr(0, 32);
            const isValid = value.length > 0;
            return {data: resultValue, isValid};
        }
        case 'newPassword': {
            const resultValue = value.substr(0, 24);
            const isValid = value.length > 7;
            return {data: resultValue, isValid};
        }
        case 'newPassword2': {
            const resultValue = value.substr(0, 24);
            return {data: resultValue, isValid: null};
        }
        default: {
            return {data: value, isValid: false};
        }
    }
}

const dashboardAppSettingsValidator = (field, value) => {
    switch (field) {
        case 'Object.wireless3GConfigs.isSupported': {
            switch (value) {
                case 'true':
                case 'false':
                case 'auto': {
                    return {data: value, isValid: true};
                }
                default: {
                    return {data: value, isValid: false};
                }
            }
        }

        case 'Object.loggerLevel': {
            return {data: value, isValid: true};
        }

        default: {
            return {data: value, isValid: false};
        }
    }
}

export {
    appManagerSettingsValidator,
    appPasswordSettingsValidator,
    dashboardAppSettingsValidator
}
