import React from 'react';

import TextLabel from '../../common/text-label';
import './global-conf-form.css';

const GlobalConfForm = ({
	data={},
	currentData={},
	isDataValid={},
	onChangeLabel,
	onToggleCheckbox,
	active=true,
	onSubmit,
	localization
}) => {
	const { loraGlobalGateway } = localization;
	const {
		server_address='',
		serv_port_up='',
		serv_port_down
	} = data;

	const {
		server_address: currentAddress = '',
		serv_port_up: currentPortUp = '',
		serv_port_down: currentPortDown = ''
	} = currentData;

	const {
		server_address: isAdressValid = false,
		serv_port_up: isPortUpValid = false,
		serv_port_down: isPortDownValid = false
	} = isDataValid;

	let lablesActive = active;

	return (
		<div className="global-conf-form container-fluid">

			<TextLabel
				title={loraGlobalGateway.serverAddress.title}
				dataDescription={{
					description: loraGlobalGateway.serverAddress.description,
					requires: loraGlobalGateway.serverAddress.validRequires
				}}
				fieldName="server_address"
				fieldValue={server_address}
				currentFieldValue={currentAddress}
				isValid={isAdressValid}
				notValidDescription={loraGlobalGateway.serverAddress.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={loraGlobalGateway.servPortUp.title}
				dataDescription={{
					description: loraGlobalGateway.servPortUp.description,
					requires: loraGlobalGateway.servPortUp.validRequires
				}}
				fieldName="serv_port_up"
				fieldValue={serv_port_up}
				currentFieldValue={currentPortUp}
				isValid={isPortUpValid}
				notValidDescription={loraGlobalGateway.servPortUp.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={loraGlobalGateway.servPortDown.title}
				dataDescription={{
					description: loraGlobalGateway.servPortDown.description,
					requires: loraGlobalGateway.servPortDown.validRequires
				}}
				fieldName="serv_port_down"
				fieldValue={serv_port_down}
				currentFieldValue={currentPortDown}
				isValid={isPortDownValid}
				notValidDescription={loraGlobalGateway.servPortDown.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

		</div>
	)
}

const GlobalConfFormButtons = ({ setConfigs, onReRequestData, dataState, localization }) => {
	const { loraGlobalGateway } = localization;
	const setConfigsButton = dataState === 'READY' ? <li><button className="btn btn-success" onClick = {setConfigs}>{loraGlobalGateway.buttons.save}</button></li> : null;
	return (
		<div className = "bottom-buttons col-xs-12">
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
						{setConfigsButton}
                        <li><button className="btn btn-default" onClick = {onReRequestData}>{loraGlobalGateway.buttons.reRequest}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
	)
}

export { GlobalConfForm, GlobalConfFormButtons };
