import React from 'react';
import { connect } from 'react-redux';

import { withLocalization } from '../../../hoc';

import { toggleExpertMode } from '../../../../actions';

import CheckBox from "react-animated-checkbox";

import './expert-mode-toggle.css';

const ExpertModeToggle = ({ expertMode, toggleExpertMode, localization }) => {
    return (
        <div className="expert-mode-toggle">
            <span>
                {localization.expertMode}:
            </span>

            <CheckBox
                checked={expertMode}
                checkBoxStyle={{
                    checkedColor: "#f82",
                    size: 18,
                    unCheckedColor: "#02d0c0"
                }}
                style={null}
                duration={40}
                onClick={toggleExpertMode}
            />
        </div>
    )
};

const mapStoreToProps = ({ expertMode }) => {
    return {
        expertMode
    }
};

const mapDispatchToProps = {
    toggleExpertMode
}

export default connect(mapStoreToProps, mapDispatchToProps)(withLocalization(ExpertModeToggle));
