import React from 'react';

import PageFrame from '../../../common/page-frame';
import PageSpoiler from '../../../common/page-spoiler';

import './layout.css';

const AppSettingsLayout = ({
    appManagerSettings,
    appDashboardSettings,
    appPasswordSettings,
    localization,
    title
}) => {
    return (
        <PageFrame name={title}>
            <div className="app-settings-layout row">
                {appPasswordSettings}
                <PageSpoiler title={localization.appManager.title} uniqName="appManagerSettings">
                    {appManagerSettings}
                </PageSpoiler>
                <PageSpoiler title={localization.appDashboard.title} uniqName="appDashboardSettings">
                    {appDashboardSettings}
                </PageSpoiler>
            </div>
        </PageFrame>
    )
};

export default AppSettingsLayout;
