import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import MainMenu from '../../../containers/specific/main-menu';

import GlobalConfPage from '../../../containers/pages/global-conf';
import NetworkConfPage from '../../../containers/pages/network';
import FrequencyConfPage from '../../../containers/pages/frequency-configs/frequency-conf-page';
import FrequencyPresetsPage from '../../../containers/pages/frequency-configs/frequency-presets-page';
import Conf3GPage from '../../../containers/pages/3G-conf';
import AboutPage from '../../../containers/pages/about';

import AppSettings from '../../../containers/pages/app-settings';

import DeviseActions from '../../../containers/pages/device-actions';


import './protected-content-view.css';

const ProtectedContentView = ({ expertMode }) => {
    const FrequencyPage = expertMode ? FrequencyConfPage : FrequencyPresetsPage;
    return (
		<div className="protected-content">
			<Router>
				<div className="menu col-md-2">
					<MainMenu />
				</div>
				<div className="content col-md-10">
					<Switch>
						<Route
							path='/'
							exact
							component={GlobalConfPage}
						/>
                        <Route
							path='/frequency'
							exact
							component={FrequencyPage}
						/>
						<Route
							path='/network'
							exact
							component={NetworkConfPage}
						/>
                        <Route
							path='/3g'
							exact
							component={Conf3GPage}
						/>
                        <Route
							path='/about'
							exact
							component={AboutPage}
						/>
                        <Route
							path='/app-settings'
							exact
							component={AppSettings}
						/>
                        <Route
                            path='/actions'
                            exact
                            component={DeviseActions}
                        />
					</Switch>
				</div>
			</Router>
		</div>
	)
}

export default ProtectedContentView;
