import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../../utils/compose';

import { withApiService, withLocalization } from '../../../../hoc';

import { isObjectValid } from '../../../../../utils/page-data-validators';
import {
    fetchFrequencyConfigs,
    frequencyConfigsEdit,
    additionalFrequencyConfigsEdit,
    frequencyOpenConfirm,
	frequencyCloseConfirm,
    sendFrequencyConfigs,
    onSavePresetFile,
    onOpenPresetFile
} from '../../../../../actions';

import MainPage from '../../../../views/common/main-page';
import Spinner from '../../../../views/common/spinner';
import ErrorIndicator from '../../../../views/common/error-indicator';
import ConfirmForm from '../../../../views/common/confirm-form';
import { FrequencyConfForm, FrequencyConfFormButtons } from '../../../../views/pages/frequency-configs/frequency-conf-form';
import {
    FileActionsButtons
} from '../../../../views/pages/frequency-configs/frequency-expert-presets-form';
import FrequencyConfigsPresetExpert from '../frequency-configs-preset-expert';

const FrequencyConfPage = ({
    loginToken,
    dataState,
    data,
    currentData,
    additionalChannels,
    currentAdditionalChannels,
    isDataValid,
    descriptionOfNotValid: descriptionOfNotValidFunction,
    radios,
    currentRadios,
    isNeedConfirm,
    confirmText,
    confirmAction,
    error,
    selectedItem,
    presetsData,

    fetchFrequencyConfigs,
    frequencyConfigsEdit,
    additionalFrequencyConfigsEdit,
    frequencyOpenConfirm,
	frequencyCloseConfirm,
    sendFrequencyConfigs,
    onSavePresetFile,
    onOpenPresetFile,

    ApiService,
    localization
}) => {
    const { loraGlobalFrequency } = localization;

    /*
    * prepare data to render
    */
    let content;

    //  data for confirm
    const sendConfigsConfirmData = {
        confirmText: loraGlobalFrequency.confirmReqests.onSave,
        confirmAction: () => sendFrequencyConfigs(data, radios, additionalChannels, isDataValid, loginToken, ApiService)
    }
    const reRequestConfigsConfirmData = {
        confirmText: loraGlobalFrequency.confirmReqests.onReRequest,
        confirmAction: () => fetchFrequencyConfigs(loginToken, ApiService)
    }

    //  Actions

    const sendConfigs = () => {
        const isValid = isObjectValid(isDataValid);
        if (isValid) {
            frequencyOpenConfirm(sendConfigsConfirmData)
        } else {
            alert(localization.confirm.notValidFields);
        }
    }


    switch (dataState) {
        case 'EMPTY': {
            fetchFrequencyConfigs(loginToken, ApiService);
            break;
        }
        case 'READY': {
            let descriptionOfNotValid = {};
            if (dataState === 'READY') {
                descriptionOfNotValid = descriptionOfNotValidFunction(loraGlobalFrequency);
            }

            //  conversion 'if' to 'frequency'
            // TODO: use util
            let inFrequencyData = {};
            let inFrequencyCurrentData = {};
            for (var key in data) {
                const radioFreq = radios['radio_'+data[key].radio].freq;
                const currentRadioFreq = currentRadios['radio_'+ currentData[key].radio].freq;
                inFrequencyData = {
                    ...inFrequencyData,
                    [key]: {
                        ...data[key],
                        if: data[key].if + radioFreq
                    }
                };
                inFrequencyCurrentData = {
                    ...inFrequencyCurrentData,
                    [key]: {
                        ...currentData[key],
                        if: currentData[key].if + currentRadioFreq
                    }
                };
            }

            const onChange = (channelName, field) => (value) => {
                frequencyConfigsEdit( field, channelName, value, inFrequencyData, radios )
            };

            const onChangeAdditional = (channelName, field, radioFreq) => (value) => {
                additionalFrequencyConfigsEdit(field, channelName, value, radioFreq);
            };

            //  prepare array for view components
            let arr = [];
            for (var channelName in data) {
                arr = [...arr, {
                    channelName: channelName,
                    isValid: isDataValid['radio_'+data[channelName].radio],
                    descriptionOfNotValid: descriptionOfNotValid[channelName],

                    enable: data[channelName].enable,
                    if: inFrequencyData[channelName].if,
                    radio: data[channelName].radio,

                    currentEnable: currentData[channelName].enable,
                    currentIf: inFrequencyCurrentData[channelName].if,
                    currentRadio: currentData[channelName].radio,

                    onChange
                }]
            }



            content = (
                <React.Fragment>
                    <FileActionsButtons
                        onSavePresetFile={() => onSavePresetFile(presetsData[selectedItem])}
                        onOpenPresetFile={onOpenPresetFile(localization)}
                        localization={localization}
                        active={!isNeedConfirm}
                    />
                    <div style={{clear: 'both'}}></div>
                    <FrequencyConfigsPresetExpert
                        active={!isNeedConfirm}
                        localization={localization}
                    />
                    <FrequencyConfForm
                        arr={arr}
                        active={!isNeedConfirm}
                        radios={radios}
                        localization={localization}
                        onChangeAdditional={onChangeAdditional}
                        additionalChannels={additionalChannels}
                    />
                </React.Fragment>
            );
            break;
        }
        case 'LOADING': content = <Spinner />
            break;
        case 'ERROR': content = <ErrorIndicator error={error}/>
            break;
        default: content = null;
    }

    let buttons = null;
	if (isNeedConfirm) {
		buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>frequencyCloseConfirm()}/>
	} else {
        buttons = <FrequencyConfFormButtons
            dataState={dataState}
            onSetConfigs={sendConfigs}
            onReRequestData={()=>frequencyOpenConfirm(reRequestConfigsConfirmData)}
            localization={localization}
        />;
    }

    return (
        <MainPage
            name={loraGlobalFrequency.title}
            content={content}
            buttons={buttons}
            error={dataState !== 'ERROR' ? error : null}
        />
    )
};

const mapStoreToProps = ({ loginToken, frequency_conf, frequency_presets }) => {
    const {
        dataState,
        data,
    	currentData,
        additionalChannels,
        currentAdditionalChannels,
        isDataValid,
        descriptionOfNotValid,
        radios,
        currentRadios,
    	isNeedConfirm,
    	confirmText,
    	confirmAction,
        error
    } = frequency_conf;
    const {
        selectedItem,
        presetsData
    } = frequency_presets;
    return {
        loginToken,
        dataState,
        data,
    	currentData,
        additionalChannels,
        currentAdditionalChannels,
        isDataValid,
        descriptionOfNotValid,
        radios,
        currentRadios,
    	isNeedConfirm,
    	confirmText,
    	confirmAction,
        error,

        selectedItem,
        presetsData
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        fetchFrequencyConfigs: (loginToken, ApiService) => fetchFrequencyConfigs(loginToken, ApiService)(dispatch),
        frequencyConfigsEdit: (field, channelName, value, inFrequencyData, radios) => dispatch(frequencyConfigsEdit(field, channelName, value, inFrequencyData, radios)),
        additionalFrequencyConfigsEdit: (field, channelName, value, radioFreq) => dispatch(additionalFrequencyConfigsEdit(field, channelName, value, radioFreq)),
        frequencyOpenConfirm: (payload) => dispatch(frequencyOpenConfirm(payload)),
    	frequencyCloseConfirm: () => dispatch(frequencyCloseConfirm()),
        sendFrequencyConfigs: (data, radios, additionalChannels, isDataValid, loginToken, ApiService) => sendFrequencyConfigs(data, radios, additionalChannels, isDataValid, loginToken, ApiService)(dispatch),

        onSavePresetFile: (configs) => onSavePresetFile(configs),
    	onOpenPresetFile: (localization) => () => onOpenPresetFile(dispatch, localization)
    }
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(FrequencyConfPage);
