import React from 'react';

import ErrorIndicator from '../error-indicator';

import './main-page.css';

const MainPage = ({ name, dataState, content, buttons, footer, error = null }) => {
    const err = (error !== null) ? <ErrorIndicator error={error} /> : null;
    return (
        <div className = "main-page container-fluid">
            <div className="main-page-title centered" >
                {name}
            </div>
            <div className="col-lg-12 main-page-row">
                {err}
            </div>
            <div className="col-lg-12 main-page-row">
                {content}
            </div>
            <div className="main-page-form-line container-fluid"></div>
            <div className="col-lg-12 main-page-row main-page-buttons-block">
                {buttons}
            </div>
            <div className="col-lg-12 main-page-row">
                {footer}
            </div>
        </div>
    )
}

export default MainPage;
