import { deepMap } from '../utils/objects-utils/deep-map-obj';
import { conf3GValidator, isObjectValid } from '../utils/page-data-validators';

// TODO: create action
const clear3GConfigs = () => {
	return {
		type: 'CLEAR_3G_CONFIGS'
	}
}

const configs3GRequested = () => {
	return {
		type: 'FETCH_3G_CONFIGS_REQUEST'
	}
}

const configs3GLoaded = (data) => {
	const isValid = deepMap(data, (key, val) => {
		const result = conf3GValidator(key, val).isValid;
		return (result[key]);
	});
	const payload = {
		data,
		isValid
	}
	return {
		type: 'FETCH_3G_CONFIGS_SUCCESS',
		payload
	}
}

const configs3GFailure = (payload) => {
	return {
		type: 'FETCH_3G_CONFIGS_FAILURE',
		payload
	}
}

const configs3GError = (payload) => {
	return {
		type: '3G_CONFIGS_ERROR',
		payload
	}
}

const configs3GEdit = (data, currentData, field, value) => {
	const payload = conf3GValidator(field, value);
	return {
		type: 'EDIT_3G_CONFIGS',
		payload
	}
}

const configs3GSaved = () => {
	return {
		type: 'SAVED_3G_CONFIGS'
	}
}

const configs3GOpenConfirm = (payload) => {
	return {
		type: '3G_OPEN_CONFIRM',
        payload
	}
}

const configs3GCloseConfirm = () => {
	return {
		type: '3G_CLOSE_CONFIRM'
	}
}

const send3GConfigs = (data, isDataValid, loginToken, ApiService) => (dispatch) => {
	const head = {
		cmd: 'set_3g_conf',
		loginToken
	};
	if(isObjectValid(isDataValid)) {
		dispatch(configs3GCloseConfirm());

		ApiService.configs3GRequest(head, data)
			.then((response) => {
				if (response.result) {
					fetch3GConfigs(loginToken, ApiService)(dispatch);
				} else {
					dispatch(configs3GError(response.msg));
				}
			})
			.catch((er) => {
				console.error("ERROR", er);
	            dispatch(configs3GError('cant_connect_to_server'));
			});
	} else {
		console.log('isDataValid = false');
	}
}

const fetch3GConfigs = (loginToken, ApiService) => (dispatch) => {
	    const head = {
        cmd: 'get_3g_conf',
        loginToken
    };
    dispatch(configs3GRequested());
    ApiService.configs3GRequest(head)
        .then((response) => {
			if (response.result) {
				const data = response.data;
				dispatch(configs3GLoaded(data))
			} else {
				dispatch(configs3GFailure(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(configs3GFailure('cant_connect_to_server'));
        });
}


export {
	clear3GConfigs,
	configs3GError,
	configs3GEdit,
	configs3GSaved,
    configs3GOpenConfirm,
    configs3GCloseConfirm,

    fetch3GConfigs,
	send3GConfigs
}
