import React from 'react';
import { connect } from 'react-redux';

import { withApiService } from '../../hoc';

import { fetchDeviceLanguage, setDeviceLanguage } from '../../../actions';

import LocalizationToggle from '../../views/specific/localization-toggle';

const LocalizationToggleContainer = ({ language, langFromDeviceState, fetchDeviceLanguage, setDeviceLanguage, ApiService }) => {
    if (langFromDeviceState === 'EMPTY') {
        fetchDeviceLanguage(ApiService);
    }
    const onSetLanguage = (lang) => {
        setDeviceLanguage(ApiService, lang);
    }
    return (
        <LocalizationToggle selected={language} onSelect={onSetLanguage}/>
    )
}

const mapStoreToProps = ({ language, langFromDeviceState }) => {
    return {
        language,
        langFromDeviceState
    }
}

const mapDispatchToProps = {
    fetchDeviceLanguage,
    setDeviceLanguage
}

export default connect(mapStoreToProps, mapDispatchToProps)(withApiService(LocalizationToggleContainer));
