import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';
import {
    configs3GEdit,
    configs3GOpenConfirm,
    configs3GCloseConfirm,
    fetch3GConfigs,
    send3GConfigs
} from '../../../../actions';

import { isObjectValid } from '../../../../utils/page-data-validators';

import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';
import ConfirmForm from '../../../views/common/confirm-form';
import MainPage from '../../../views/common/main-page';
import { Form3GConf, Form3GConfButtons } from '../../../views/pages/3g-conf';

const Conf3GPage = ({
    loginToken,
    expertMode,
    isSupport3G,
    dataState,
    data,
	currentData,
    isDataValid,
	isNeedConfirm,
	confirmText,
	confirmAction,
    error,

    configs3GEdit,
    configs3GOpenConfirm,
    configs3GCloseConfirm,
    fetch3GConfigs,
    send3GConfigs,

    ApiService,
    localization
}) => {
    const { $3G } = localization;
    const setConfigs = () => {
        const isValid = isObjectValid(isDataValid);
		if (isValid) {
			configs3GOpenConfirm(setConfigsConfirmData)
		} else {
            alert(localization.confirm.notValidFields);
        }
    }

    const onChangeLabel = (field, value) => {
        if (!isNeedConfirm) {
			configs3GEdit(data, currentData, field, value);
		}
    }

    const setConfigsConfirmData = {
        confirmText: $3G.confirmReqests.onSave,
        confirmAction: () => send3GConfigs(data, isDataValid, loginToken, ApiService)
    }

    const reRequestData = {
        confirmText: $3G.confirmReqests.onReRequest,
        confirmAction: () => fetch3GConfigs(loginToken, ApiService)
    }

    if (dataState === 'EMPTY') {
		fetch3GConfigs(loginToken, ApiService);
	}

    let content = null;
    let buttons = null;

    if (isSupport3G) {
        switch (dataState) {
            case 'READY':
                content = <Form3GConf
                    isExpertMode={expertMode}
                    data={data}
                    currentData={currentData}
                    isDataValid={isDataValid}
                    onChangeLabel={onChangeLabel}
                    active={!isNeedConfirm}
                    localization={localization}
                    onSubmit={setConfigs}
                    />
                break;
            case 'LOADING': content = <Spinner />
                break;
            case 'ERROR': content = <ErrorIndicator error={error}/>
                break;
            default: content = null;
        }

        if (isNeedConfirm) {
            buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={() => configs3GCloseConfirm()}/>
        } else {
            buttons = <Form3GConfButtons
                dataState={dataState}
                setConfigs={setConfigs}
                onReRequestData={() => configs3GOpenConfirm(reRequestData)}
                localization={localization}
            />
        }
    }

    return (
        <MainPage name={$3G.title} content={content} buttons={buttons} error={dataState !== 'ERROR' ? error : null} />
    )
}

const mapStoreToProps = ({ loginToken, expertMode, conf_3G, isSupport3G }) => {
    const {
		dataState,
		data,
		currentData,
		isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

		error
	} = conf_3G;
    return {
        loginToken,
        expertMode,
        isSupport3G,
        dataState,
        data,
		currentData,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    }
}

const mapDispatchToProps = (dispatch) => {
	return {
		fetch3GConfigs: (loginToken, ApiService) => fetch3GConfigs(loginToken, ApiService)(dispatch),
		send3GConfigs: (data, isDataValid, loginToken, ApiService) => send3GConfigs(data, isDataValid, loginToken, ApiService)(dispatch),
		configs3GEdit: (data, currentData, field, value) => dispatch(configs3GEdit(data, currentData, field, value)),
		configs3GOpenConfirm: (payload) => dispatch(configs3GOpenConfirm(payload)), // { confirmText, confirmAction }
		configs3GCloseConfirm: () => dispatch(configs3GCloseConfirm())
	}
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(Conf3GPage);
