import React from 'react';

import LightTextLabel from '../../../common/light-text-label';
import DropingList from '../../../common/droping-list';

import './manager-panel.css';

const AppSettingsManagerPanel = ({
    data,
    onChange,
    onSubmit,
    isValid,
    isActive,
    localization
}) => {
    const {
        BS_DASHBOARD = {},
        BS_DASHBOARD_MANAGER = {},
        UPDATE = {}
    } = data;

    return (
        <div className="app-settings-manager-panel page-list">
            <div className="group-title col-md-12">
                {localization.dashboardUpdateSources}:
            </div>
            {
                fetchFieldsArr(
                    BS_DASHBOARD,
                    isValid.BS_DASHBOARD,
                    onChange('BS_DASHBOARD'),
                    onSubmit,
                    isActive,
                    localization
                )
            }

            <div className="group-title col-md-12">
                {localization.managerUpdateSources}:
            </div>
            {
                fetchFieldsArr(
                    BS_DASHBOARD_MANAGER,
                    isValid.BS_DASHBOARD_MANAGER,
                    onChange('BS_DASHBOARD_MANAGER'),
                    onSubmit,
                    isActive,
                    localization
                )
            }

            <div className="group-title col-md-12">
                {localization.UPDATE}:
            </div>
            {
                fetchFieldsArr(
                    UPDATE,
                    isValid.UPDATE,
                    onChange('UPDATE'),
                    onSubmit,
                    isActive,
                    localization
                )
            }
            <div className="group-title col-md-12">
                {localization.other}:
            </div>
            <div className="container-fluid item">
                <div className="item-title col-md-3">
                    {localization.SERVER_STARTUP_METHOD.title}
                </div>
                <div className="item-value col-md-9">
                    <DropingList
                        value={data.SERVER_STARTUP_METHOD}
                        isActive={isActive}
                        isValid={isValid.SERVER_STARTUP_METHOD}
                        description={localization.SERVER_STARTUP_METHOD.description}
                        handleChange={onChange()('SERVER_STARTUP_METHOD')}
                        list={[
                            {name: localization.SERVER_STARTUP_METHOD.values.button, value: 'button'},
                            {name: localization.SERVER_STARTUP_METHOD.values.automatically, value: 'automatically'}
                        ]}
                    />
                </div>
            </div>
            <div className="container-fluid item">
                <div className="item-title col-md-3">
                    {localization.loggerLevel.title}
                </div>
                <div className="item-value col-md-9">
                    <DropingList
                        value={data.loggerLevel}
                        isActive={isActive}
                        isValid={isValid.loggerLevel}
                        description={localization.loggerLevel.description}
                        handleChange={onChange()('loggerLevel')}
                        list={[
                            {name: localization.loggerLevel.values.error, value: 'error'},
                            {name: localization.loggerLevel.values.warn, value: 'warn'},
                            {name: localization.loggerLevel.values.info, value: 'info'},
                            {name: localization.loggerLevel.values.verbose, value: 'verbose'},
                            {name: localization.loggerLevel.values.debug, value: 'debug'},
                            {name: localization.loggerLevel.values.silly, value: 'silly'}
                        ]}
                    />
                </div>
            </div>
        </div>
    )
}

const field = (name, value, isValid, onChange, onSubmit, isActive, localization) => {
    let fieldInput = null;
    if (name === 'GIT_PROVIDER') {
        fieldInput = <DropingList
            value={value}
            isActive={isActive}
            description={localization[name].description}
            handleChange={onChange}
            list={[{name: localization[name].values.GIT_HUB, value: 'GIT_HUB'}, {name: localization[name].values.GIT_LAB, value: 'GIT_LAB'}]}
        />
    } else {
        fieldInput = <LightTextLabel
            value={value}
            isActive={isActive}
            onChange={onChange}
            description={localization[name].description}
            isValid={isValid}
            onSubmit={onSubmit}
        />
    }
    return (
        <div className="container-fluid item" key={name}>
            <div className="item-title col-md-3">
                {localization[name].title}
            </div>
            <div className="item-value col-md-9">
                {fieldInput}
            </div>
        </div>
    )
}

const fetchFieldsArr = (obj, isValid, onChange, onSubmit, isActive, localization) => {
    let arr = [];
    for (let key in obj) {
        arr = [
            ...arr,
            field(key, obj[key], isValid[key], onChange(key), onSubmit, isActive, localization)
        ]
    }
    return arr;
}

export default AppSettingsManagerPanel;
