import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';

import {
    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm
} from '../../../../actions';

import ConfirmForm from '../../../views/common/confirm-form';

import DeviceActionsLayout from '../../../views/pages/device-actions/layout';
import DeviceActionsButton from '../../../views/pages/device-actions/button';

const DeviseActions = ({
    loginToken,
    expertMode,
    isNeedConfirm ,confirmText, confirmAction,
    deviceActionsError,

    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm,

    localization,
    ApiService
}) => {
    const { deviceActions: deviceActionsLocal = {} } = localization;

    const rebootDeviseConfirmData = {
        confirmText: deviceActionsLocal.rebootDevice.confirmReqest,
        confirmAction: () => rebootDeviceAction(loginToken, ApiService, localization)
    };
    const rebootDashboardConfirmData = {
        confirmText: deviceActionsLocal.rebootBsDashboard.confirmReqest,
        confirmAction: () => rebootDashboardAction(loginToken, ApiService, localization)
    };
    const shutdownDashboardConfirmData = {
        confirmText: deviceActionsLocal.shutdownBsDashboard.confirmReqest,
        confirmAction: () => shutdownDashboardAction(loginToken, ApiService, localization)
    };
    const setFactorySettingsConfirmData = {
        confirmText: deviceActionsLocal.resetAllSettings.confirmReqest,
        confirmAction: () => setFactorySettingsAction(loginToken, ApiService, localization)
    };
    const setFactoryLoRaConfirmData = {
        confirmText: deviceActionsLocal.resetLoraSettings.confirmReqest,
        confirmAction: () => setFactoryLoraSettingsAction(loginToken, ApiService, localization)
    };

    const confirmer = isNeedConfirm ? <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>mainCloseConfirm()}/> : null;

    const deviceActionsButtons = [
        <DeviceActionsButton key="1" label={deviceActionsLocal.rebootDevice.title} description={deviceActionsLocal.rebootDevice.description} action={()=>mainOpenConfirm(rebootDeviseConfirmData)}/>
    ];

    const dashboardActionsButtons = [
        <DeviceActionsButton key="1" label={deviceActionsLocal.rebootBsDashboard.title} description={deviceActionsLocal.rebootBsDashboard.description}  action={()=>mainOpenConfirm(rebootDashboardConfirmData)}/>,
        <DeviceActionsButton key="2" label={deviceActionsLocal.shutdownBsDashboard.title} description={deviceActionsLocal.shutdownBsDashboard.description}  action={()=>mainOpenConfirm(shutdownDashboardConfirmData)}/>
    ];

    const filesActionsButtons = [
        <DeviceActionsButton key="2" label={deviceActionsLocal.resetLoraSettings.title} description={deviceActionsLocal.resetLoraSettings.description}  action={()=>mainOpenConfirm(setFactoryLoRaConfirmData)}/>,
        <DeviceActionsButton key="1" label={deviceActionsLocal.resetAllSettings.title} description={deviceActionsLocal.resetAllSettings.description}  action={()=>mainOpenConfirm(setFactorySettingsConfirmData)}/>
    ];

    return (
        <DeviceActionsLayout
            localization={deviceActionsLocal}
            title={deviceActionsLocal.title}
            deviceActionsButtons={deviceActionsButtons}
            dashboardActionsButtons={expertMode ? dashboardActionsButtons : null}
            filesActionsButtons={filesActionsButtons}
            confirmer={confirmer}
            error={deviceActionsError}
        />
    )
}

const mapStoreToProps = ({loginToken, expertMode, isNeedConfirm ,confirmText, confirmAction, deviceActionsError}) => {
    return {
        loginToken,
        expertMode,
        deviceActionsError,
        isNeedConfirm ,confirmText, confirmAction
    }
}

const mapDispatchToProps = {
    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(DeviseActions);
