import checkDataDifferent from '../utils/check-data-different';

const initialNetworkConfStore = {
	dataState: 'EMPTY',
	error: null,
	currentData: {},
	data: {},
	isDataValid: {},
	isChanged: false,
	isNeedConfirm: false,
	confirmText: null,
	confirmAction: null
}

const networkConfReducer = (networkConfStore = initialNetworkConfStore, action) => {
	switch (action.type) {
		case 'CLEAR_NETWORK_CONFIGS': {
			return initialNetworkConfStore;
		}
		case 'FETCH_NETWORK_CONFIGS_REQUEST': {
			return {
				...initialNetworkConfStore,
				dataState: 'LOADING',
			}
		}
		case 'FETCH_NETWORK_CONFIGS_SUCCESS': {
			const { payload } = action;
			return {
				...initialNetworkConfStore,
				dataState: 'READY',
				currentData: payload.data,
				data: payload.data,
				isDataValid: payload.isValid
			}
		}
		case 'FETCH_NETWORK_CONFIGS_FAILURE': {
			const { payload } = action;
			return {
				...networkConfStore,
				dataState: 'ERROR',
				error: payload
			}
		}
		case 'NETWORK_CONFIGS_ERROR': {
			const { payload } = action;
			return {
				...networkConfStore,
				error: payload
			}
		}
		case 'EDIT_NETWORK_CONFIGS': {
			const { payload } = action;

			const result = {
				...networkConfStore,
				data: {
					...networkConfStore.data,
					...payload.data
				},
				isDataValid: {
					...networkConfStore.isDataValid,
					...payload.isValid
				}
			}

			const isChanged = checkDataDifferent(result);

			return {
				...result,
				isChanged
			}
		}
		// TODO: rewrite or delete
		case 'SAVED_NETWORK_CONFIGS': {
			return {
				...networkConfStore,
				//isChanged: false
			}
		}
		case 'NETWORK_OPEN_CONFIRM': {
			const { payload } = action;
			const { confirmText, confirmAction } = payload;
			return {
				...networkConfStore,
				isNeedConfirm: true,
				confirmText,
				confirmAction
			}
		}
		case 'NETWORK_CLOSE_CONFIRM': {
			return {
				...networkConfStore,
				isNeedConfirm: false,
				confirmText: null,
				confirmAction: null
			}
		}
		default: return networkConfStore;
	}
}

export default networkConfReducer;
