import React from 'react';

import ChannelSettings from '../channel-settings';
import AdditionalChannel from '../additional-channels';
// TODO: delete components
//import TextRow from '../common/text-row';

import './frequency-conf-form.css';

const FrequencyConfForm = ({
    arr=[],
    additionalChannels,
    active=true,
    radios={},
    onChangeAdditional,
    localization
}) => {
    const { loraGlobalFrequency } = localization;


    const elements = arr.map((el) => {
        return (
            <ChannelSettings
                key={el.channelName}
                title={loraGlobalFrequency.configs[el.channelName]}
            	dataDescription = {loraGlobalFrequency.dataDescription}

            	channelName={el.channelName}
                listRadio={[
                    {value: 0, name: "radio_0", label: loraGlobalFrequency.configs["radio_0"]},
                    {value: 1, name: "radio_1", label: loraGlobalFrequency.configs["radio_1"]}
                ]}

            	channelValue={el.if}
            	isActiveChannel={el.enable}
            	selectedRadio={el.radio}

            	currentFieldValue={'if=' + el.currentIf + ' Enable=' + el.currentEnable + ' Radio=' + el.currentRadio}

            	isValid={el.isValid}
            	notValidDescription={el.descriptionOfNotValid !== undefined ? el.descriptionOfNotValid : null}

            	onChange={el.onChange}

            	active={active}
            />
        )
    })

    /*const {
        radio_0,
        radio_1
    } = radios;*/
    return (
        <div className="col-lg-12 frequency-conf-form">
            {/*<TextRow label={`radio_0.freq = ${radio_0.freq}`} color="#dcf3d7" />
            <TextRow label={`radio_1.freq = ${radio_1.freq}`} color="#d4eff4" />*/}
            {elements}

            <div className="row"></div>

            <button className="btn additional-channels-spoiler-button" type="button" data-toggle="collapse" data-target="#collapseAddChannels" aria-expanded="false" aria-controls="collapseAddChannels">
                {loraGlobalFrequency.additionalChannelsSpoiler}<i className="fa fa-caret-down" aria-hidden="true"></i>
            </button>

            <div className="collapse" id="collapseAddChannels">
                <AdditionalChannel
                    title="ChanLoraStd"
                    dataDescription={{}}
                    radios={radios}

                    channelName="chan_Lora_std"
                    listRadio={[
                        {value: 0, name: "radio_0", label: loraGlobalFrequency.configs["radio_0"]},
                        {value: 1, name: "radio_1", label: loraGlobalFrequency.configs["radio_1"]}
                    ]}

                    frequency={additionalChannels.chan_Lora_std.if + radios['radio_' + additionalChannels.chan_Lora_std.radio].freq}
                    isActiveChannel={additionalChannels.chan_Lora_std.enable}
                    selectedRadio={additionalChannels.chan_Lora_std.radio}
                    bandwidth={additionalChannels.chan_Lora_std.bandwidth}
                    field1={additionalChannels.chan_Lora_std.spread_factor}

                    freqFieldName="frequency"
                    bandwidthFieldName="bandwidth"
                    field1FieldName="spread_factor"

                    isValid="true"
                    notValidDescription=""

                    onChange={onChangeAdditional}

                    active={active}
                />
                <AdditionalChannel
                    title="ChanFSK"
                    dataDescription={{}}
                    radios={radios}

                    channelName="chan_FSK"
                    listRadio={[
                        {value: 0, name: "radio_0", label: loraGlobalFrequency.configs["radio_0"]},
                        {value: 1, name: "radio_1", label: loraGlobalFrequency.configs["radio_1"]}
                    ]}

                    frequency={additionalChannels.chan_FSK.if + radios['radio_' + additionalChannels.chan_FSK.radio].freq}
                    isActiveChannel={additionalChannels.chan_FSK.enable}
                    selectedRadio={additionalChannels.chan_FSK.radio}
                    bandwidth={additionalChannels.chan_FSK.bandwidth}
                    field1={additionalChannels.chan_FSK.datarate}

                    freqFieldName="frequency"
                    bandwidthFieldName="bandwidth"
                    field1FieldName="datarate"

                    isValid="true"
                    notValidDescription={null}

                    onChange={onChangeAdditional}

                    active={active}
                />
            </div>
        </div>
    )
}


const FrequencyConfFormButtons = ({ dataState, onSetConfigs, onReRequestData, localization }) => {
    const { loraGlobalFrequency } = localization;
    const setConfigsButton = dataState === 'READY' ? <li><button className="btn btn-success" onClick = {onSetConfigs}>{loraGlobalFrequency.buttons.save}</button></li> : null;
	return (
        <div className = "bottom-buttons col-xs-12">
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
						{setConfigsButton}
						<li><button className="btn btn-default" onClick = {onReRequestData}>{loraGlobalFrequency.buttons.reRequest}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
	)
}


export { FrequencyConfForm, FrequencyConfFormButtons }
