import deepMerge from '../utils/objects-utils/deep-merge';

const initialAppSettingsStore = {
    managerSettings: {
        dataState: 'EMPTY',
        data: {},
        error: null,
        isNeedConfirm: false,
        confirmText: null,
        confirmAction: null
    },
    dashboardSettings: {
        dataState: 'EMPTY',
        data: {},
        error: null,
        isNeedConfirm: false,
        confirmText: null,
        confirmAction: null
    },
    passwordSettings: {
        data: {
            currentPassword: '',
            newPassword: '',
            newPassword2: ''
        },
        isValid: {
            currentPassword: false,
            newPassword: false,
            newPassword2: true
        },
        error: null,
        isNeedConfirm: false,
        confirmText: null,
        confirmAction: null
    }
}

const appSettingsReducer = (appSettingsStore = initialAppSettingsStore, action) => {
    const { type, payload } = action;
    switch (type) {

        /**
         * Manager settings reducer
         */
        case 'FETCH_MANAGER_APP_SETTINGS_REQUEST': {
            return {
                ...appSettingsStore,
                managerSettings: {
                    ...initialAppSettingsStore.managerSettings,
                    dataState: 'LOADING',
                }
            }
        }
		case 'FETCH_MANAGER_APP_SETTINGS_SUCCESS': {

            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    dataState: 'SUCCESS',
                    data: payload.data,
                    isValid: payload.isValid,
                    error: null
                }
            }
        }
		case 'FETCH_MANAGER_APP_SETTINGS_FAILURE': {
            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    dataState: 'ERROR',
                    data: {},
                    error: payload
                }
            }
        }
        case 'MANAGER_APP_SETTINGS_ERROR': {
            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    error: payload
                }
            }
        }
        case 'EDIT_MANAGER_APP_SETTINGS': {
            let isValidData;
            let dataData;
            if (payload.part === undefined) {
                isValidData = {
                    [payload.field]: payload.isValid
                };
                dataData = {
                    [payload.field]: payload.val
                };
            } else {
                isValidData = {
                    [payload.part]:{[payload.field]: payload.isValid}
                };
                dataData = {
                    [payload.part]:{[payload.field]: payload.val}
                };
            }

            const isValid = deepMerge(
                appSettingsStore.managerSettings.isValid,
                isValidData
            );
            const data = deepMerge(
                appSettingsStore.managerSettings.data,
                dataData
            );

            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    data,
                    isValid
                }
            }
        }
        case 'MANAGER_APP_SETTINGS_OPEN_CONFIRM': {
            const { confirmText, confirmAction } = payload;
            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    isNeedConfirm: true,
                    confirmText,
                    confirmAction
                }
            }
        }
        case 'MANAGER_APP_SETTINGS_CLOSE_CONFIRM': {
            return {
                ...appSettingsStore,
                managerSettings: {
                    ...appSettingsStore.managerSettings,
                    isNeedConfirm: false,
                    confirmText: null,
                    confirmAction: null
                }
            }
        }

        /**
         * Password settings reducer
         */
        case 'PASSWORD_SETTINGS_EDIT': {
            const { field, result } = payload;

            let isPasswordsSame;
            switch (field) {
                case 'newPassword': {
                    isPasswordsSame = {newPassword2: (result.data === appSettingsStore.passwordSettings.data.newPassword2)};
                    break;
                }
                case 'newPassword2': {
                    isPasswordsSame = {newPassword2: (result.data === appSettingsStore.passwordSettings.data.newPassword)};
                    break;
                }
                default: {
                    isPasswordsSame = {};
                }
            }

            return {
                ...appSettingsStore,
                passwordSettings: {
                    ...appSettingsStore.passwordSettings,
                    data: {
                        ...appSettingsStore.passwordSettings.data,
                        [field]: result.data
                    },
                    isValid: {
                        ...appSettingsStore.passwordSettings.isValid,
                        [field]: result.isValid,
                        ...isPasswordsSame
                    }
                }
            }
        }

        case 'APP_PASSWORD_SETTINGS_ERROR': {
            return {
                ...appSettingsStore,
                passwordSettings: {
                    ...appSettingsStore.passwordSettings,
                    error: payload
                }
            }
        }

        case 'APP_PASSWORD_SETTINGS_CLEAN': {
            return {
                ...appSettingsStore,
                passwordSettings: {
                    ...initialAppSettingsStore.passwordSettings
                }
            }
        }

        case 'APP_PASSWORD_SETTINGS_OPEN_CONFIRM': {
            const { confirmText, confirmAction } = payload;
            return {
                ...appSettingsStore,
                passwordSettings: {
                    ...appSettingsStore.passwordSettings,
                    isNeedConfirm: true,
                    confirmText,
                    confirmAction
                }
            }
        }
        case 'APP_PASSWORD_SETTINGS_CLOSE_CONFIRM': {
            return {
                ...appSettingsStore,
                passwordSettings: {
                    ...appSettingsStore.passwordSettings,
                    isNeedConfirm: false,
                    confirmText: null,
                    confirmAction: null
                }
            }
        }

        /**
         * Dashboard settings reducer
         */
        case 'FETCH_APP_DASHBOARD_SETTINGS_REQUEST': {
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...initialAppSettingsStore.dashboardSettings,
                    dataState: 'LOADING',
                }
            }
        }
        case 'FETCH_APP_DASHBOARD_SETTINGS_SUCCESS': {
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...appSettingsStore.dashboardSettings,
                    dataState: 'SUCCESS',
                    data: payload.data,
                    isValid: payload.isValid,
                    error: null
                }
            }
        }
        case 'FETCH_APP_DASHBOARD_SETTINGS_FAILURE': {
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...appSettingsStore.dashboardSettings,
                    dataState: 'ERROR',
                    data: {},
                    error: payload
                }
            }
        }

        case 'FETCH_APP_DASHBOARD_SETTINGS_ERROR':{
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...appSettingsStore.dashboardSettings,
                    error: payload
                }
            }
        }

        case 'EDIT_DASHBOARD_APP_SETTINGS': {
            const {
                part,
                field,
                isValid,
                val
            } = payload;
            if (part !== undefined) {
                return {
                    ...appSettingsStore,
                    dashboardSettings: {
                        ...appSettingsStore.dashboardSettings,
                        data: {
                            ...appSettingsStore.dashboardSettings.data,
                            [part]: {
                                ...appSettingsStore.dashboardSettings.data[part],
                                [field]: val
                            }
                        },
                        isValid: {
                            ...appSettingsStore.dashboardSettings.isValid,
                            [part]: {
                                ...appSettingsStore.dashboardSettings.isValid[part],
                                [field]: isValid
                            }
                        }
                    }
                }
            } else {
                return {
                    ...appSettingsStore,
                    dashboardSettings: {
                        ...appSettingsStore.dashboardSettings,
                        data: {
                            ...appSettingsStore.dashboardSettings.data,
                            [field]: val
                        },
                        isValid: {
                            ...appSettingsStore.dashboardSettings.isValid,
                            [field]: isValid
                        }
                    }
                }
            }

        }

        case 'DASHBOARD_APP_SETTINGS_OPEN_CONFIRM':{
            const { confirmText, confirmAction } = payload;
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...appSettingsStore.dashboardSettings,
                    isNeedConfirm: true,
                    confirmText,
                    confirmAction
                }
            }
        }
		case 'DASHBOARD_APP_SETTINGS_CLOSE_CONFIRM':{
            return {
                ...appSettingsStore,
                dashboardSettings: {
                    ...appSettingsStore.dashboardSettings,
                    isNeedConfirm: false,
                    confirmText: null,
                    confirmAction: null
                }
            }
        }


        default: return {
            ...initialAppSettingsStore
        }
    }
}

export default appSettingsReducer;
