import checkDataDifferent from '../utils/check-data-different';

const initialConf3GStore = {
	dataState: 'EMPTY',
	error: null,
	currentData: {},
	data: {},
	isDataValid: {},
	isChanged: false,
	isNeedConfirm: false,
	confirmText: null,
	confirmAction: null
}

const conf3GReducer = (conf3GStore = initialConf3GStore, action) => {
    switch (action.type) {
        case 'CLEAR_3G_CONFIGS': {
            return initialConf3GStore;
        }
        case 'FETCH_3G_CONFIGS_REQUEST': {
            return {
                ...initialConf3GStore,
                dataState: 'LOADING',
            }
        }
        case 'FETCH_3G_CONFIGS_SUCCESS': {
            const { payload } = action;
            return {
                ...initialConf3GStore,
                dataState: 'READY',
                currentData: payload.data,
                data: payload.data,
                isDataValid: payload.isValid
            }
        }
        case 'FETCH_3G_CONFIGS_FAILURE': {
            const { payload } = action;
            return {
                ...conf3GStore,
                dataState: 'ERROR',
                error: payload
            }
        }
        case '3G_CONFIGS_ERROR': {
            const { payload } = action;
            return {
                ...conf3GStore,
                error: payload
            }
        }
        case 'EDIT_3G_CONFIGS': {
            const { payload } = action;

            const result = {
                ...conf3GStore,
                data: {
                    ...conf3GStore.data,
                    ...payload.data
                },
                isDataValid: {
                    ...conf3GStore.isDataValid,
                    ...payload.isValid
                }
            }

            const isChanged = checkDataDifferent(result);

            return {
                ...result,
                isChanged
            }
        }
        // TODO: rewrite or delete
        case 'SAVED_3G_CONFIGS': {
            return {
                ...conf3GStore,
                //isChanged: false
            }
        }
        case '3G_OPEN_CONFIRM': {
            const { payload } = action;
            const { confirmText, confirmAction } = payload;
            return {
                ...conf3GStore,
                isNeedConfirm: true,
                confirmText,
                confirmAction
            }
        }
        case '3G_CLOSE_CONFIRM': {
            return {
                ...conf3GStore,
                isNeedConfirm: false,
                confirmText: null,
                confirmAction: null
            }
        }
        default: return conf3GStore;
    }
    }

    export default conf3GReducer;
