import React from 'react';

import { withLocalization } from '../../../hoc';

import './header.css';

import vega from './vega.png';

import Logout from '../../../containers/logout';
//import ExpertModeToggle from '../../views/expert-mode-toggle';
// TODO: localization unit
import LocalizationToggleContainer from '../../../containers/localization-toggle-container';

const Header = ({gateway_ID, isLogin, deviceName, localization}) => {
	const gatewayId = gateway_ID === undefined ? null : <span>{localization.about.device.gatewayid}: {gateway_ID}</span>
	const logout = isLogin ? <Logout /> : null;
	//const expertModeToggle = isLogin ? <ExpertModeToggle /> : null;
    return (
        <div className=" container-fluid header">

			<div className="header-logo col-md-1">
				<img className="" alt="vega_logo" src={vega} />
			</div>

			<div className="header-title col-md-11">

	            <div className="header-text">
					<div className="title">
						{deviceName}
					</div>
					<div className="gateway-id">
						{gatewayId}
					</div>
				</div>
				<div className="header-logout">

					<div>
						{/*expertModeToggle*/}
						<LocalizationToggleContainer />
					</div>
					{logout}

				</div>
			</div>


        </div>
    )
}


export default withLocalization(Header);
