import React from 'react';

import TextLabel from '../../common/text-label';
import CheckBox from '../../common/check-box';

import './network-conf-form.css';

const NetworkConfForm = ({
	isExpertMode=false,
	data={},
	isDataValid={},
	currentData={},
	onChangeLabel,
	onToggleCheckbox,
	active=true,
	localization,
	onSubmit
}) => {
	const { deviceNetwork } = localization;
	const {
		eth0='',
		address='',
		netmask='',
		gateway
	} = data;

	const {
		eth0: currentEth0 = '',
		address: currentAddress = '',
		netmask: currentNetmask = '',
		gateway: currentGateway = ''
	} = currentData;

	const {
		eth0: isEth0Valid = false,
		address: isAddressUpValid = false,
		netmask: isNetmaskValid = false,
		gateway: isGatewayValid = false
	} = isDataValid;

	let checkBoxActive = active;
	let lablesActive = eth0 !== 'dhcp';
	lablesActive = active ? lablesActive : false;

	const expertModeFields = isExpertMode ? <ExpertModeFields
		data={data}
		isDataValid={isDataValid}
		currentData={currentData}
		onChangeLabel={onChangeLabel}
		active={lablesActive}
		localization={localization}
		onSubmit={onSubmit}
	/> : null;

	return (
		<div className="network-conf-form container-fluid">

			<CheckBox
				title={deviceNetwork.isStatic.title}
				dataDescription={{description: deviceNetwork.isStatic.description}}
				fieldName="eth0"
				fieldValue={eth0 === 'static'}
				currentFieldValue={currentEth0 === 'static'}
				isValid={isEth0Valid}
				notValidDescription={deviceNetwork.isStatic.validRequires}
				onToggleCheckbox={onToggleCheckbox}
				active={checkBoxActive}
			/>

			<TextLabel
				title={deviceNetwork.address.title}
				dataDescription={{
					description: deviceNetwork.address.description,
					requires: deviceNetwork.address.validRequires
				}}
				fieldName="address"
				fieldValue={address}
				currentFieldValue={currentAddress}
				isValid={isAddressUpValid}
				notValidDescription={deviceNetwork.address.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.netmask.title}
				dataDescription={{
					description: deviceNetwork.netmask.description,
					requires: deviceNetwork.netmask.validRequires
				}}
				fieldName="netmask"
				fieldValue={netmask}
				currentFieldValue={currentNetmask}
				isValid={isNetmaskValid}
				notValidDescription={deviceNetwork.netmask.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.gateway.title}
				dataDescription={{
					description: deviceNetwork.gateway.description,
					requires: deviceNetwork.gateway.validRequires
				}}
				fieldName="gateway"
				fieldValue={gateway}
				currentFieldValue={currentGateway}
				isValid={isGatewayValid}
				notValidDescription={deviceNetwork.gateway.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			{expertModeFields}

		</div>
	)
}

const ExpertModeFields = ({
	data={},
	isDataValid={},
	currentData={},
	onChangeLabel,
	active=true,
	localization,
	onSubmit
}) => {
	const { deviceNetwork } = localization;
	const {
		domain,
		nameserver0,
		nameserver1
	} = data;

	const {
		domain: currentDomain = '',
		nameserver0: currentDns0 = '',
		nameserver1: currentDns1 = ''
	} = currentData;

	const {
		domain: isDomainValid = false,
		nameserver0: isDns0Valid = false,
		nameserver1: isDns1Valid = false
	} = isDataValid;

	return (
		<React.Fragment>
			<TextLabel
				title={deviceNetwork.domain.title}
				dataDescription={{
					description: deviceNetwork.domain.description,
					requires: deviceNetwork.domain.validRequires
				}}
				fieldName="domain"
				fieldValue={domain}
				currentFieldValue={currentDomain}
				isValid={isDomainValid}
				notValidDescription={deviceNetwork.gateway.validRequires}
				onChangeLabel={onChangeLabel}
				active={active}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.nameserver.title + ' 1'}
				dataDescription={{
					description: deviceNetwork.nameserver.description,
					requires: deviceNetwork.nameserver.validRequires
				}}
				fieldName="nameserver0"
				fieldValue={nameserver0}
				currentFieldValue={currentDns0}
				isValid={isDns0Valid}
				notValidDescription={deviceNetwork.nameserver.validRequires}
				onChangeLabel={onChangeLabel}
				active={active}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.nameserver.title + ' 2'}
				dataDescription={{
					description: deviceNetwork.nameserver.description,
					requires: deviceNetwork.nameserver.validRequires
				}}
				fieldName="nameserver1"
				fieldValue={nameserver1}
				currentFieldValue={currentDns1}
				isValid={isDns1Valid}
				notValidDescription={deviceNetwork.nameserver.validRequires}
				onChangeLabel={onChangeLabel}
				active={active}
				onSubmit={onSubmit}
			/>
		</React.Fragment>
	)
}

const NetworkConfFormButtons = ({ dataState, setConfigs, setDefault, onReRequestData, localization }) => {
	const { deviceNetwork } = localization;
	const setConfigsButton = dataState === 'READY' ? <li><button className="btn btn-success" onClick = {setConfigs}>{deviceNetwork.buttons.save}</button></li> : null;
	return (
		<div className = "bottom-buttons col-xs-12">
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
						{setConfigsButton}
                        <li><button className="btn btn-default" onClick = {onReRequestData}>{deviceNetwork.buttons.reRequest}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
	)
}

export { NetworkConfForm, NetworkConfFormButtons };
